import React, { useEffect,useState } from 'react';
import {Link,useParams} from 'react-router-dom'
import Layout from '../../components/Layout'
import FacultyTab from '../../components/Faculty/FacultyTab'
import { Col } from 'reactstrap';
import styles from '../../css/About.css'
import { toast } from "react-toastify";

import XLSX from 'xlsx';
import { getCookie } from '../../auth/window';
import MyInput from '../../components/MyInput2'

import axios from 'axios';
import { Button, Modal, ModalHeader, ModalBody, ModalFooter } from 'reactstrap';

const UserDashboard=()=>{
    const {id}=useParams()
    const token=getCookie('token')

    const [quiz,setQuiz]=useState([])
    const [modal, setModal] = useState(false);
    const [modal1, setModal1] = useState(false);
    const [modal2, setModal2] = useState(false);
    const [modal3, setModal3] = useState(false);
    const [error, setError] = useState("");
    const [question, setQuestion] = useState("");
    const [option1, setOption1] = useState("");
    const [option2, setOption2] = useState("");
    const [option3, setOption3] = useState("");
    const [option4, setOption4] = useState("");
    const [answer, setAnswer] = useState("");
    const [email, setEmail] = useState("");
    const [questions, setQuestions] = useState([]);
    const [que, setQue] = useState([]);
    const [loading, setLoading] = useState(false);
    const [students, setStudents] = useState([])
    const [allStudents, setAllStudents] = useState([])
    const [result, setResult] = useState([])
    const toggle = () => setModal(!modal);
    const toggle1 = () => setModal1(!modal1);
    const toggle2 = () => setModal2(!modal2);
    const toggle3 = () => setModal3(!modal3);

    let stime;

    useEffect(()=>{
        ViewQuiz()       
        viewQuestion()    
        viewResult()
    },[])

    const ViewQuiz = () =>{
        axios.get(`${process.env.REACT_APP_API}/quiz/${id}`,{
            headers:{
                Authorization:`Bearer ${token}`,
                "Content-Type":"application/json"
            }
        }).then(res=>{
            setQuiz(res.data.Quiz[0])
            setAllStudents(res.data.Quiz[0].students)
            
        })
        .catch(err => console.log(err))
    }

    const addQuestion = (e) => {
        e.preventDefault()
        if(!question || !option1 || !option2 || !option3 || !option4 || !answer){
            setError("Please enter all fields")
            return;
        }
        if(answer <1 && answer >4){
            setError("Please enter answer in given range")
            return 
        }
        axios.post(`${process.env.REACT_APP_API}/create-question`,{
            id, question, option1, option2,option3,option4, answer
        },{
            headers:{
                Authorization:`Bearer ${token}`,
                "Content-Type":"application/json"
            }
        }).then(res => {
            setQuestion('')
            setOption1('')
            setOption2('')
            setOption3('')
            setOption4('')
            setAnswer('')  
            viewQuestion()      
            toggle()
        })
        .catch(err => console.log(err))
    }

    const viewQuestion =() =>{ 
        axios.get(`${process.env.REACT_APP_API}/question-by-quiz/${id}`,{
            headers:{
                Authorization:`Bearer ${token}`,
                "Content-Type":"application/json"
            }
        }).then((res)=>{
            setQue(res.data.q)            
        })
        .catch(err => console.log(err))
    }

    const readExcel=(file)=>{
        const filereader = new FileReader();
        filereader.readAsArrayBuffer(file)
        filereader.onload=(e)=>{
            const bufferArray=e.target.result
            const wb=XLSX.read(bufferArray,{type:'buffer'});
            const ws=wb.Sheets[wb.SheetNames[0]];

            const data=XLSX.utils.sheet_to_json(ws);
            for(let i=2;i<data.length+2;i++){
                students.push(ws[`A${i}`].v);
                
            }
        }
    }

    const UploadEmail = (e)=>{
        e.preventDefault();
        setLoading(true);
        axios.put(`${process.env.REACT_APP_API}/update-students`,{students,id:quiz._id},{
            headers:{
                Authorization:`Bearer ${token}`,
                "Content-Type":"application/json"
            }
        } ).then(res => {
            setLoading(false)   
                toggle1()
                setStudents([])
                ViewQuiz()
        })
        .catch(err => console.log(err))

    }

    const addStudent = (e) => {
        e.preventDefault()
        if(!email){
            setError('Please enter the email')
            return
        }
        else if(!/^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/.test(email)){
            setError('Please Enter valid Email id')
            return
        }
        axios.put(`${process.env.REACT_APP_API}/add-student`,{email,id:quiz._id},{
            headers: {
                Authorization:`Bearer ${token}`,
                "Content-Type": "application/json"
            }
        })
        .then(res => {
            toggle2()
            ViewQuiz()
            toast.success(`${email} added Successfully`, {
                position: "top-center",
                });
            setEmail('')
        })
        .catch(err => console.log(err))
        
    }

    const readQuestion=(file)=>{
        const filereader = new FileReader();
        filereader.readAsArrayBuffer(file)
        filereader.onload=(e)=>{
            const bufferArray=e.target.result
            const wb=XLSX.read(bufferArray,{type:'buffer'});
            const ws=wb.Sheets[wb.SheetNames[0]];

            const data=XLSX.utils.sheet_to_json(ws);
            setQuestions(data);
        }
    }

    const addQuestionExcel = (e) => {
        e.preventDefault();
        setLoading(true);
        axios.put(`${process.env.REACT_APP_API}/add-questions-excel`,{questions,id:quiz._id},{
            headers:{
                Authorization:`Bearer ${token}`,
                "Content-Type":"application/json"
            }
        } ).then(res => {
            setLoading(false)   
            toggle3()
            setQuestions([])
            viewQuestion()
        })
        .catch(err => console.log(err))
    }

    const viewResult = () => {
        axios.get(`${process.env.REACT_APP_API}/result-quiz/${id}`, {
          headers:{
            'Content-Type': 'application/json',
            Authorization: `Bearer ${token}`
          }
        }).then(res => {
          console.log(res.data.result,"hhhhhhhh")
          setResult(res.data.result)
        })
        .catch(err=>console.log(err))
      }
    

    return (
    
        <Layout>
            <Col md="9" className="back2">
    
            <div className="aboutText" style={{paddingBottom:"25px"}}>
            <h4 style={{fontSize:"25px",fontWeight:"600"}}>Quiz Name: {quiz.name}</h4>
            <br/>
            <h5 style={{fontSize:"18px",float:"left",fontWeight:"500"}}>
                <p>No of Questions: {quiz.no_of_questions}</p>
                </h5>
            <h5 style={{fontSize:"18px",float:"right",fontWeight:"500",margin:"0",textAlign: 'left'}}>
                Start Time: {quiz.startTime? quiz.startTime.split('T')[0]:""} at  {quiz.startTime?quiz.startTime.split('T')[1]:""} 
               
                <br/>
                Duration: {quiz.duration} min
                </h5>
            <br/>
            </div>
            <div style={{textAlign: 'center',paddingBottom:"10px",backgroundColor:"#cf404d"}}>
            <button className="homebtn profilebtn2"
            onClick={toggle}
                    style={{marginTop:"10px"}}
                    >
                        Add new Question</button> 
            <button className="homebtn profilebtn2"             
                    style={{marginTop:"10px"}}
                    onClick={toggle2}
                    >
                        Add new Student</button> 
            
            <button className="homebtn profilebtn2"             
                    style={{marginTop:"10px"}}
                    onClick={toggle3}
                    >
             Add Questions from excel</button> 
            <button className="homebtn profilebtn2"             
                    style={{marginTop:"10px"}}
                    onClick={toggle1}
                    >
             Add Student from excel</button> 
           
                        
            
            </div>
            <FacultyTab id={id} que={que} viewQuestion={viewQuestion} allStudents={allStudents} ViewQuiz={ViewQuiz} quiz={quiz} result={result} />
            </Col>
            <Modal isOpen={modal} toggle={toggle} className="my-modal mb-4 "  size="lg"
                centered={true}
                style={{textAlign: 'center' }}
                >
                <ModalBody >
                    <div className="mt-2 mb-4" style={{fontSize:"30px"}}> Add New Quiz </div>
                    {error?<div className="alert alert-danger">{error}</div>:""}
                    <textarea 
                    placeholder="Question" 
                    type="text"
                    value={question} 
                    rows="3"
                    style={{width:"100%"}}
                    onChange={e=>setQuestion(e.target.value)} />
                    <MyInput label={"Option 1"} type={"text"}
                     value={option1} setValue={setOption1} clas={'Active'}/>
                    <MyInput label={"Option 2"} type={"text"}
                     value={option2} setValue={setOption2} clas={'Active'}/>
                    <MyInput label={"Option 3"} type={"text"}
                     value={option3} setValue={setOption3} clas={'Active'}/>
                    <MyInput label={"Option 4"} type={"text"}
                     value={option4} setValue={setOption4} clas={'Active'}/>
                    <MyInput label={"Answer (value from 1,2,3,4)"} type={"number"}
                     value={answer} setValue={setAnswer} clas={'Active'}/>



                </ModalBody>
                <div style={{textAlign: 'center',paddingBottom:"20px"}}> 
                  <Button color="homebtn profilebtn"
                  style={{fontSize:"20px", padding:"5px 20px", fontFamily:"cursive"}}
                  onClick={e=>addQuestion(e)}
                  >
                      Save</Button>{'   '}
                  <Button
                  style={{fontSize:"20px", padding:"5px 20px", fontFamily:"cursive"}} 
                  color="homebtn profilebtn2" onClick={toggle}>Cancel</Button>
                  </div>
              </Modal>

              <Modal isOpen={modal1} toggle={toggle1} className="my-modal mb-4 "  size="lg"
                centered={true}
                style={{textAlign: 'center', maxWidth:"500px" }}
                >
                <ModalBody >
                    <div className="mt-2 mb-2" style={{fontSize:"20px"}}> Email id must be in the first column and first row in column must be a column title </div>
                    
                </ModalBody>
                {loading==true?<div className="homebtn homebtn3 m-4" style={{cursor: "none"}}> Please Wait File is Uploading...</div>
            :
            <div>
                <div style={{textAlign: 'center',paddingBottom:"20px"}}> 
                <label className="mb-4 homebtn signinbtn"

                        style={{textAlign:"center"}}
                        >
                        Upload File
                        <input
                            type="file"
                            hidden
                            onChange={e=>readExcel(e.target.files[0])}
                        />
                        </label>
                        <br/>
                  <Button color="homebtn profilebtn"
                  style={{fontSize:"20px", padding:"5px 20px", fontFamily:"cursive"}}
                  onClick={e=>UploadEmail(e)}
                  >
                      Save
                      </Button>{'   '}
                  <Button
                  style={{fontSize:"20px", padding:"5px 20px", fontFamily:"cursive"}} 
                  color="homebtn profilebtn2" onClick={toggle1}>Cancel</Button>
                  </div>
                  </div>}
              </Modal>

              <Modal isOpen={modal2} toggle={toggle2} className="my-modal mb-4 "  size="lg"
                centered={true}
                style={{textAlign: 'center', maxWidth:"600px" }}
                >
                <ModalBody >
                    <div className="mt-2 mb-4" style={{fontSize:"30px"}}> Add New Student </div>
                    {error?<div className="alert alert-danger">{error}</div>:""}
                    
                    <MyInput label={"Email"} type={"text"}
                     value={email} setValue={setEmail} clas={'Active'}/>
                    
                </ModalBody>
                <div style={{textAlign: 'center',paddingBottom:"20px"}}> 
                  <Button color="homebtn profilebtn"
                  style={{fontSize:"20px", padding:"5px 20px", fontFamily:"cursive"}}
                  onClick={e=>addStudent(e)}
                  >
                      Save</Button>{'   '}
                  <Button
                  style={{fontSize:"20px", padding:"5px 20px", fontFamily:"cursive"}} 
                  color="homebtn profilebtn2" onClick={toggle2}>Cancel</Button>
                  </div>
              </Modal>

              <Modal isOpen={modal3} toggle={toggle3} className="my-modal mb-4 "  size="lg"
                centered={true}
                style={{textAlign: 'center', maxWidth:"500px" }}
                >
                <ModalBody >
                    <div className="mt-2 mb-2" style={{fontSize:"16px",fontWeight:"700", textAlign: "left"}}> 
                    <ul>
                        <li>First row in each column must be a column title</li>
                        <li>First Column will be question with title <strong>'Question'</strong></li>
                        <li>Second Column will be Option 1 with title <strong>'Option1'</strong></li>
                        <li>Third Column will be Option 2 with title <strong>'Option2'</strong></li>
                        <li>Fourth Column will be Option 3 with title <strong>'Option3'</strong></li>
                        <li>Fifth Column will be Option 4 with title <strong>'Option4'</strong></li>
                        <li>Sixth Column will be Answer in range of 1 to 4 with title <strong>'Answer'</strong></li>
                    </ul> 
                    </div>
                    
                </ModalBody>
                {loading==true?<div className="homebtn homebtn3 m-4" style={{cursor: "none"}}> Please Wait File is Uploading...</div>
            :
            <div>
                <div style={{textAlign: 'center',paddingBottom:"20px"}}> 
                <label className="mb-4 homebtn signinbtn"

                        style={{textAlign:"center"}}
                        >
                        Upload File
                        <input
                            type="file"
                            hidden
                            onChange={e=>readQuestion(e.target.files[0])}
                        />
                        </label>
                        <br/>
                  <Button color="homebtn profilebtn"
                  style={{fontSize:"20px", padding:"5px 20px", fontFamily:"cursive"}}
                  onClick={e=>addQuestionExcel(e)}
                  >
                      Save
                      </Button>{'   '}
                  <Button
                  style={{fontSize:"20px", padding:"5px 20px", fontFamily:"cursive"}} 
                  color="homebtn profilebtn2" onClick={toggle3}>Cancel</Button>
                  </div>
                  </div>}
              </Modal>


        </Layout>
    )
}

export default UserDashboard;
