import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Accordion from '@material-ui/core/Accordion';
import AccordionDetails from '@material-ui/core/AccordionDetails';
import AccordionSummary from '@material-ui/core/AccordionSummary';
import AccordionActions from '@material-ui/core/AccordionActions';
import Typography from '@material-ui/core/Typography';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import Button from '@material-ui/core/Button';
import Divider from '@material-ui/core/Divider';

import EditIcon from '@material-ui/icons/Edit';
import DeleteSweepIcon from '@material-ui/icons/DeleteSweep';

const useStyles = makeStyles((theme) => ({
  root: {
    width: '100%',
    marginBottom:"25px",

  },
  heading: {
    fontSize: theme.typography.pxToRem(15),
    flexBasis: '100%',
    flexShrink: 0,
  }
  
}));

export default function ControlledAccordions({q,handleEditQuestion,deleteQuestion,i}) {
  const classes = useStyles();
  const [expanded, setExpanded] = React.useState(false);

  const handleChange = (panel) => (event, isExpanded) => {
    setExpanded(isExpanded ? panel : false);
  };

  return (
    <div className={classes.root} key={q._id}>
      <Accordion expanded={expanded === `panel${q._id}`} onChange={handleChange(`panel${q._id}`)} style={{backgroundColor:"rgb(241, 239, 232)"}}>
        <AccordionSummary
          expandIcon={<ExpandMoreIcon />}       
        >
          <div className={classes.heading}
          style={{color:"black",fontWeight:"600",fontSize:"18px",}}
          >
           <span style={{color:"#cf404d"}}> Question {i+1}: </span> {q.question}</div>
        </AccordionSummary>
        <AccordionDetails className="pt-0 pb-0">
          <div>
          <div className="container"  style={{padding:"5px 0px ", marginBottom:"10px"}}>
                <div className="row m-0">
          <div className="col-md-10 offset-1" style={{border:"0px solid black",color:"#2D3E4F",fontWeight:"bold"}}>
                    <p className="m-0">Option 1 : {q.option1} </p>
                    </div>
                    <div className="col-md-10 offset-1" style={{border:"0px solid black",color:"#2D3E4F",fontWeight:"bold"}}>
                    <p className="m-0">Option 2 : {q.option2} </p>
                    </div>
                    <div className="col-md-10 offset-1" style={{border:"0px solid black",color:"#2D3E4F",fontWeight:"bold"}}>
                    <p className="m-0">Option 3 : {q.option3} </p>
                    </div>
                    <div className="col-md-10 offset-1" style={{border:"0px solid black",color:"#2D3E4F",fontWeight:"bold"}}>
                    <p className="m-0">Option 4 : {q.option4} </p>
                    </div>
                    
                    </div>
                    </div>
          </div>
        </AccordionDetails>
       
        <AccordionActions>
        <div className="col-md-10" style={{border:"0px solid black",color:"black",fontWeight:"bold"}}>
                       Answer: <span style={{color:"#cf404d"}}>{q.answer}</span> 
                    </div>
          <Button size="small">
          <EditIcon style={{fontSize:"30px", cursor:"pointer", color:"#2d3e4f"}}
                       onClick={e => handleEditQuestion(e,q)}/>
          </Button>
          <Button size="small" color="primary">
          <DeleteSweepIcon style={{fontSize:"30px", cursor:"pointer", color:"#cf404d"}}
                    onClick={e=>deleteQuestion(e,q._id)} />
          </Button>
        </AccordionActions>
      </Accordion>

      
     
    </div>
  );
}