import React, { useState }  from 'react';
import Layout from '../../components/Layout'
import { Col } from 'reactstrap';
import {Link,useHistory,useParams} from 'react-router-dom'
import MyInput from '../../components/MyInput'
import axios from 'axios'
import { toast } from "react-toastify";

const ResetPassword = () => {
    const history =useHistory()
    const [password, setPassword] = useState('');
    const [confirmPassword, setConfirmPassword] = useState('');
    const [error,setError] = useState('')
    const {token}=useParams()

    const handleSubmit=(e)=>{
        e.preventDefault();
        if(password!==confirmPassword || password===""){
            setError("Password does not match!")
            return;
        }else {
            setError("")
            axios.put(`${process.env.REACT_APP_API}/reset`,{password},{
                headers:{
                    "Content-Type": "application/json",
                    Authorization:`Bearer ${token}`
                }
            })
            .then(res=>{
                if(!res.data.error){
                    setError('')
                    toast.success('Password Reset Successfully', {
                        position: "top-center",
                        });
                    history.push("/signin")
                }else{
                    setError(res.data.error)
                }
            }).catch(err=>{
                console.error(err)
                setError("Enter valid password")
            })
             }
    }
    return (
        <div style={{background:`url("https://res.cloudinary.com/quizee/image/upload/v1625901107/Profile/ubfowehmrnl4dceymxke.png")`
        ,  backgroundSize:"125px 80px",height:"100%",position:"fixed",width:"100%"}}>
 
            <div className="animate__animated animate__pulse backsignin1">
            <h1 className="signinhead">Reset Password</h1>
            {error?<div className="alert alert-danger">{error}</div>:""}
            <MyInput label={"Password"} type={"password"} value={password} setValue={setPassword}/>
            <MyInput label={"Confirm Password"} type={"password"} value={confirmPassword} setValue={setConfirmPassword}/>
            <br/>
            <Link to="/signin"><button className="homebtn signinbtn" onClick={e=>handleSubmit(e)}>Submit</button></Link>

            </div>
            
        </div> 
        
       
    )
  }
  
  export default ResetPassword;