import React from 'react';
import Layout from '../../components/Layout'
import MyInput from '../../components/MyInput'
import { Col } from 'reactstrap';
import { useState } from 'react';
import axios from 'axios';
import {Link,useHistory} from 'react-router-dom'
import { toast } from "react-toastify";
import LoginGoogle from '../../components/LoginGoogle';

import {authenticate} from '../../auth/window'
const Signin=()=> {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [message,setMessage]=useState('')
  const [error,setError]=useState('')

  const history=useHistory();

  const handleSubmit=(e)=>{
    e.preventDefault();
    
    if(!email || ! password){
        setError("Please Enter email and password");
        return
    }
    axios.post(`${process.env.REACT_APP_API}/signin`,{email,password},{
      header:{
        "content-type":"application/json"
      }
    })
    .then(res=>{
        if(!res.data.error){
      setEmail('')
      setPassword('')
      setError("")
      setMessage(res.data.message)
      
      const data={
        token:res.data.signinToken,
        user:{
          email:res.data.email,
          number:res.data.number,
          dob:res.data.dob,
          role:res.data.role,
          name:res.data.name,
          pic:res.data.pic,
          id:res.data._id
        }
      }
      authenticate(data)
      toast.success('Sign in Successfully', {
        position: "top-center",
        });
      if(res.data.role ==='0')
        history.push("/admindashboard")
      else if(res.data.role ==='1')
        history.push("/facultydashboard")
      else
        history.push("/dashboard")
 
        }
        else{
        setError(res.data.error)
        setMessage('')
        }
    })
    .catch(err=>{
      console.log(err,"aman")
      setError("Invalid credentials !")
      setMessage("")
    })
  }
  
    return (
        <Layout>
            <Col md="9" className="backsignin">
            <div className="animate__animated animate__pulse backsignin1">
            <h1 className="signinhead">SignIn</h1>
            {error?<div className="alert alert-danger">{error}</div>:""}
            {message?<div className="alert alert-info">{message}</div> : ''}
            <LoginGoogle/>
            <MyInput label={"E-Mail"} type={"email"} value={email} setValue={setEmail}/>
            <MyInput label={"Password"} type={"password"} value={password} setValue={setPassword}/>
            <br/>
            <Link to="/signin"><button className="homebtn signinbtn" onClick={e=>handleSubmit(e)}>Sign In</button></Link>
           
            <div style={{paddingBottom:"50px",marginTop:"10px"}}>
            <span style={{float:"left"}}>
            <Link to="/signup"><span className="signinspan">New User?</span></Link>
            </span>
            <span style={{float:"right"}}>
            <Link to="/forgotPassword"><span className="signinspan">Forgotten Password?</span></Link>
            </span>
            </div>
            </div>
        
            </Col>
        </Layout>   
    )
  }
  
  export default Signin;