import React from 'react';
import {Route,Redirect} from 'react-router-dom';
import {isAuth} from './window';

export const AdminRoute=({component:Component,...rest})=>(
    <Route {...rest} render={props=>isAuth() && (isAuth().role==="0" || isAuth().role==="19") ? 
        (
            <Component {...props}/>
        ):(

            <Redirect to={{
                pathname:"/signin",
                state:{from:props.location}
            }}
            />
        )}/>
)

export const FacultyRoute=({component:Component,...rest})=>(
    <Route {...rest} render={props=>isAuth() && isAuth().role==="1" ? 
        (
            <Component {...props}/>
        ):(

            <Redirect to={{
                pathname:"/signin",
                state:{from:props.location}
            }}
            />
        )}/>
)

export const PrivateRoute=({component:Component,...rest})=>(
    <Route {...rest} render={props=>isAuth() ? 
        (
            <Component {...props}/>
        ):(

            <Redirect to={{
                pathname:"/signin",
                state:{from:props.location}
            }}
            />
        )}/>
)

export const AuthRoute=({component:Component,...rest})=>(
    <Route {...rest} render={props=>!isAuth() ? 
        (
            <Component {...props}/>
        ):
        isAuth().role==='0' || isAuth().role==="19" ?
        (
            <Redirect to={{
                pathname:"/admindashboard",
                state:{from:props.location}
            }}
            />
        ):
        isAuth().role==='1' ?
        (
            <Redirect to={{
                pathname:"/facultydashboard",
                state:{from:props.location}
            }}
            />
        ):
        (
            <Redirect to={{
                pathname:"/dashboard",
                state:{from:props.location}
            }}
            />
        )
    }/>
)

