import React from 'react';
import Layout from '../../components/Layout'
import { Col } from 'reactstrap';
import styles from '../../css/About.css'
import { Button, Modal, ModalHeader, ModalBody, ModalFooter } from 'reactstrap';
import { useState, useEffect } from 'react';
import MyInput from '../../components/MyInput2'
import axios from 'axios'
import { isAuth,getCookie, authenticate } from '../../auth/window';
import { toast } from "react-toastify";
import EditIcon from '@material-ui/icons/Edit';
import DeleteSweepIcon from '@material-ui/icons/DeleteSweep';
import { Link } from 'react-router-dom';
import LightTooltip from '../../components/LightTooltip';

const FacultyDashboard=()=>{

    const [modal, setModal] = useState(false);
    const [modal1, setModal1] = useState(false);
    const [modal2, setModal2] = useState(false);
    const [modal3, setModal3] = useState(false);
    const [name, setName] = useState('')
    const [duration,setDuration] = useState('')
    const [quizId,setQuizId] = useState('')
    const [numberOfQuestions, setNumberOfQuestions] = useState('')
    const [error, setError] = useState("");
    const [quiz, setQuiz] = useState("");
    const [quiz_id, setQuiz_id] = useState("")
    const [startTime, setStartTime] = useState("")

    
    let _id=isAuth().id
    let author_name=isAuth().name
    const token=getCookie('token')
    const toggle = () => setModal(!modal);
    const toggle1 = () => {
        setName("");
            setNumberOfQuestions("");
            setDuration("")
            setQuizId("")
            setStartTime('')
        setModal1(!modal1);
    }

    const toggle3 = () => {
            setQuiz_id("")
        setModal3(!modal3);
    }
  
    const createQuiz = (e) => {
        e.preventDefault();
        if(!name || !duration || !numberOfQuestions || !startTime){
            setError("Please enter all fields")
            return;
        }
        if(isNaN(duration)){
            setError("Please enter valid duration in numbers")
            return 
        }
        if(isNaN(numberOfQuestions)){
            setError("Please enter valid number of questions in numbers")
            return 
        }
        axios.post(`${process.env.REACT_APP_API}/create-quiz`, {name, no_of_questions:numberOfQuestions, author_name,duration, _id,startTime},{
            headers:{
                Authorization:`Bearer ${token}`,
                "Content-Type":"application/json"
            }
        }).then(res => {
            toast.success(`${res.data.message}`, {
                position: "top-center",
                });
            
            setError("");
            setName("");
            setStartTime('')
            setNumberOfQuestions("");
            setDuration("")
            ViewQuiz()
            toggle()
        })
        .catch(err => console.log(err))
    }

    useEffect(() => {
        ViewQuiz()
    },[])

    const ViewQuiz = () =>{
        axios.get(`${process.env.REACT_APP_API}/quiz-by-faculty/${_id}`,{
            headers: {
                Authorization: `Bearer ${token}`,
                "Content-Type":"application/json"
            }
        })
        .then(res => {
            setQuiz(res.data.Quiz);
        })
        .catch(err => console.log(err))
    }

    const handleDelete= (id,name) => {
        
        toggle3();
        setQuiz_id(id);
      }

    const deleteQuiz=(e) => {
        axios.delete(`${process.env.REACT_APP_API}/delete-quiz/${quiz_id}`,{
            headers: {
                Authorization: `Bearer ${token}`,
            }
        })
        .then(res => {
            ViewQuiz();
            toggle3()
            toast.success(`deleted Successfully`, {
                position: "top-center",
                });
        })
        .catch(err => console.error(err)) 
    }

    const EditQuiz = (q) =>{
        toggle1();
        setName(q.name)
        setNumberOfQuestions(q.no_of_questions)
        setDuration(q.duration)
        setStartTime(q.startTime)
        setQuizId(q._id)
    }

    const updateQuiz = (e) => {
        e.preventDefault();
        if(!name || !duration || !numberOfQuestions || !startTime){
            setError("Please enter all fields")
            return;
        }
        if(isNaN(duration)){
            setError("Please enter valid duration in numbers")
            return 
        }
        if(isNaN(numberOfQuestions)){
            setError("Please enter valid number of questions in numbers")
            return 
        }
        axios.put(`${process.env.REACT_APP_API}/update-quiz`,{name, no_of_questions:numberOfQuestions, duration,startTime, _id:quizId},{
            headers:{
                Authorization:`Bearer ${token}`,
                "Content-Type": "application/json"

            }
        })
        .then(res => {
            toast.success(`${name} updated Successfully`, {
                position: "top-center",
                });
                ViewQuiz()
                toggle1()
        })
        .catch(err => console.log(err)) 
    }

    
    
    return (
        <Layout>
            <Col md="9" className="back2">
            <div className="aboutText" style={{paddingBottom:"10px"}}>
                <h1 className="abouth1">Dashboard</h1>
                <button className="homebtn profilebtn2"
                    onClick={toggle}
                    style={{marginTop:"20px"}}
                    >
                        create new quiz</button>   
                                 
            </div>
          
            <div className="container-fluid">
                <div className="row">
                    <div className="col-md-3 offset-1">
                    
                    </div>
                </div>
                <div className="container-fluid"  style={{backgroundColor:"#cf404d",padding:" 0px ", marginBottom:"10px",marginTop:"10px"}}>
                    <div className="row">
                    <div className="col-8 offset-1" style={{textAlign: '',marginTop:"8px"}}>
                    <h5 style={{fontWeight:"bold", color:"white"}}>Quiz Name</h5>

                        </div>
                        <div className="col-1" style={{textAlign: 'center',marginTop:"8px"}}>
                        <h5 style={{fontWeight:"bold", color:"white"}}>Edit</h5>
                        </div>
                        <div className="col-1" style={{textAlign: 'center',marginTop:"8px"}}>
                        <h5 style={{fontWeight:"bold", color:"white"}}>Delete</h5>
                        </div>
                        
                        
                    </div>

                </div>
                    { quiz ? quiz.map((q,key)=>{
                        return(
                            <div className="container" key={key} style={{backgroundColor:"rgba(241, 241, 235, 0.705)",padding:"15px 0px ", marginBottom:"10px"}}>
                    <div className="row">
                        
                        <div className="col-8 offset-1" style={{textAlign: ''}} >
                        
                        <Link to={`/facultydashboard/${q._id}`}>
                        <LightTooltip title="Click to Edit/View Questions or Students" placement="bottom-start">
                        <h6 style={{fontWeight:"bold", color:"#2D3E4F",cursor: "pointer"}} 
                        >{q.name}</h6>
                         </LightTooltip>
                        </Link>
                       
                        </div>
                       
                        <div className="col-1" style={{textAlign: 'center',marginTop:""}}>
                           <h6 style={{fontWeight:"bold", color:"#2D3E4F"}}>
                               <EditIcon
                               style={{cursor:"pointer"}}
                               onClick={e=>EditQuiz(q)}
                               />
                           </h6>
                        </div>
                        <div className="col-1" style={{textAlign: 'center',marginTop:""}}>
                        <h6 style={{fontWeight:"bold", color:"#2D3E4F"}}>
                            <DeleteSweepIcon
                            style={{cursor:"pointer"}}
                            onClick={e=>handleDelete(q._id,q.name)}
                            />
                        </h6>
                        </div>                       
                       
                    </div>

                </div>
                        )
                    } ) 
                    :"" }
                   

            </div>
            <Modal isOpen={modal} toggle={toggle} className="my-modal mb-4 "  size="lg"
                centered={true}
                style={{textAlign: 'center', maxWidth:"600px" }}
                >
                <ModalBody >
                    <div className="mt-2 mb-4" style={{fontSize:"30px"}}> Create New Quiz </div>
                    {error?<div className="alert alert-danger">{error}</div>:""}
                    <MyInput label={"Quiz Name"} type={"text"} value={name} setValue={setName}/>
                    <MyInput label={"Duration in minutes"} type={"number"} value={duration} setValue={setDuration}/>
                    <MyInput label={"Number of Questions"} type={"text"} value={numberOfQuestions} setValue={setNumberOfQuestions}/>
                    <MyInput label={"Start Time"} type={"datetime-local"} value={startTime} setValue={setStartTime} clas={'Active'}/>


                </ModalBody>
                <div style={{textAlign: 'center',paddingBottom:"20px"}}> 
                  <Button color="homebtn profilebtn"
                  style={{fontSize:"20px", padding:"5px 20px", fontFamily:"cursive"}}
                  onClick={e => createQuiz(e)}
                  >
                      Save</Button>{'   '}
                  <Button
                  style={{fontSize:"20px", padding:"5px 20px", fontFamily:"cursive"}} 
                  color="homebtn profilebtn2" onClick={toggle}>Cancel</Button>
                  </div>
              </Modal>

            <Modal isOpen={modal1} toggle={toggle1} className="my-modal mb-4 "  size="lg"
                centered={true}
                style={{textAlign: 'center', maxWidth:"600px" }}
                >
                <ModalBody >
                    <div className="mt-2 mb-4" style={{fontSize:"30px"}}> Edit Quiz </div>
                    {error?<div className="alert alert-danger">{error}</div>:""}
                    <MyInput label={"Name"} type={"text"} value={name} setValue={setName} clas={'Active'}/>
                    <MyInput label={"Duration in minutes"} type={"number"} value={duration} setValue={setDuration} clas={'Active'}/>
                    <MyInput label={"Number of Questions"} type={"text"} value={numberOfQuestions} setValue={setNumberOfQuestions} clas={'Active'}/>
                    <MyInput label={`Start Time`} type={"datetime-local"} value={startTime} setValue={setStartTime} clas={'Active'}/>


                </ModalBody>
                <div style={{textAlign: 'center',paddingBottom:"20px"}}> 
                  <Button color="homebtn profilebtn"
                  style={{fontSize:"20px", padding:"5px 20px", fontFamily:"cursive"}}
                  onClick={e => updateQuiz(e)}
                  >
                      Save</Button>{'   '}
                  <Button
                  style={{fontSize:"20px", padding:"5px 20px", fontFamily:"cursive"}} 
                  color="homebtn profilebtn2" onClick={toggle1}>Cancel</Button>
                  </div>
              </Modal>

              <Modal isOpen={modal3} toggle={toggle3} className="my-modal mb-4 "  size="lg"
                centered={true}
                style={{textAlign: 'center', maxWidth:"600px" }}
                >
                <ModalBody >
                    <div className="mt-2 mb-4" style={{fontSize:"30px"}}> Are you sure you want to delete ? </div>
                    
                </ModalBody>
                <div style={{textAlign: 'center',paddingBottom:"20px"}}> 
                  <Button color="homebtn profilebtn"
                  style={{fontSize:"20px", padding:"5px 20px", fontFamily:"cursive"}}
                  onClick={e=>deleteQuiz(e)}>
                      Yes</Button>{'   '}
                  <Button
                  style={{fontSize:"20px", padding:"5px 20px", fontFamily:"cursive"}} 
                  color="homebtn profilebtn2" onClick={toggle3}>No</Button>
                  </div>
              </Modal>


            </Col>
        </Layout>
    )
}
export default FacultyDashboard;
