import React , { useState, useEffect }from 'react';
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/core/styles';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import axios from 'axios';
import { isAuth,getCookie, authenticate } from '../../auth/window';
import MyInput from '../MyInput'
import { Link } from 'react-router-dom';

function TabPanel(props) {
  const { children, value, index, ...other } = props;


  return (
    <div
        style={{ width: `80%`, padding:`0px 0px 0px 10px`}}
      role="tabpanel"
      hidden={value !== index}
      id={`vertical-tabpanel-${index}`}
      aria-labelledby={`vertical-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box p={3}>
          <Typography>{children}</Typography>
        </Box>
      )}
    </div>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.any.isRequired,
  value: PropTypes.any.isRequired,
};

function a11yProps(index) {
  return {
    id: `vertical-tab-${index}`,
    'aria-controls': `vertical-tabpanel-${index}`,
  };
}

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
    backgroundColor: theme.palette.background.paper,
    display: 'flex',

  },
  tabs: {
    borderRight: `1px solid ${theme.palette.divider}`,
    backgroundColor: ""
  },
}));

export default function VerticalTab() {
  const classes = useStyles();
  const token=getCookie('token')
  const [value, setValue] = React.useState(0);
  const [viewAdmin, setViewAdmin] = React.useState();
  const [email, setEmail] = React.useState();
  const [message,setMessage]=useState('')
  const [error,setError]=useState('')
  const [contact,setContact]=useState([])
  const [faculty,setFaculty]=useState([])

  useEffect(() => {
    handleContactView()
  },[])

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };
  const handleContactView=async() =>{
      await axios.get(`${process.env.REACT_APP_CONTACT_API}`)
      .then((response)=>setContact(response.data.reverse()))
      .catch((error)=>console.log(error))
  }

  const handleView=async()=>{
    await axios.get(`${process.env.REACT_APP_FACULTY_REQUEST_API}`)
      .then((response)=>

      setFaculty(response.data.reverse()))
      .catch((error)=>console.log(error))
  }

  
  return (
    <div className={classes.root}>
      <Tabs
        orientation="vertical"
        value={value}
        onChange={handleChange}
        aria-label="Vertical tabs example"
        className={classes.tabs}
      >
        <Tab className='vTab vTab1' onClick={e=>handleContactView()}  label="Contact Queries" {...a11yProps(0)} />
        <Tab className='vTab vTab1' onClick={e=>handleView()} label="Faculty Request" {...a11yProps(1)} />
        
      </Tabs>
      <TabPanel  value={value} index={0} >
      <div className="container-fluid"  style={{backgroundColor:"#cf404d",padding:" 0px ", marginBottom:"10px"}}>
                    <div className="row">
                    <div className="col-3" style={{textAlign: 'center',marginTop:"8px"}}>
                           <h5 style={{fontWeight:"bold", color:"white"}}>Name</h5>
                        </div>
                        <div className="col-6" style={{textAlign: 'center',marginTop:"8px"}}>
                           <h5 style={{fontWeight:"bold", color:"white"}}>Email id</h5>
                        </div>
                        <div className="col-3" style={{textAlign: 'center',marginTop:"8px"}}>
                        <h5 style={{fontWeight:"bold", color:"White"}}>Contact Number</h5>
                        </div>
                    </div>
                </div>
        {contact?contact.map((p,key)=>{
            return(
                <div className="container-fluid" key={key} style={{backgroundColor:"rgba(241, 241, 235, 0.705)",padding:"15px 0px ", marginBottom:"10px"}}>
                    <div className="row">
                        <div className="col-3" style={{textAlign: 'center'}}>
                        <h6 style={{fontWeight:"bold", color:"#2D3E4F"}}>{p.name}</h6>

                        </div>
                        <div className="col-6" style={{textAlign: 'center',marginTop:""}}>
                           <h6 style={{fontWeight:"bold", color:"#2D3E4F"}}>{p.email}</h6>
                        </div>
                        <div className="col-3" style={{textAlign: 'center',marginTop:""}}>
                        <h6 style={{fontWeight:"bold", color:"#2D3E4F"}}>{p.number}</h6>
                        </div>
                    </div>
                   
                    <div className="row">
                        <div className="col-12" >
                       <p style={{margin:"0px 10px"}}>
                           <span style={{fontSize:"20px", color:"#cf404d",fontWeight:"bold"}}>Query : </span>
                           {p.description}</p> 
                   </div>
                    </div>
                </div>
            )
        }):''}
      </TabPanel>
      <TabPanel value={value} index={1}>
      <div className="container-fluid"  style={{backgroundColor:"#cf404d",padding:" 0px ", marginBottom:"10px"}}>
                    <div className="row">
                    <div className="col-3" style={{textAlign: 'center',marginTop:"8px"}}>
                           <h5 style={{fontWeight:"bold", color:"white"}}>Name</h5>
                        </div>
                        <div className="col-4" style={{textAlign: 'center',marginTop:"8px"}}>
                           <h5 style={{fontWeight:"bold", color:"white"}}>Email Id</h5>
                        </div>
                        <div className="col-2" style={{textAlign: 'center',marginTop:"8px"}}>
                        <h5 style={{fontWeight:"bold", color:"White"}}>Contact Number</h5>
                        </div>
                        <div className="col-3" style={{textAlign: 'center',marginTop:"8px"}}>
                        <h5 style={{fontWeight:"bold", color:"White"}}>Aadhar Number</h5>
                        </div>
                    </div>
                </div>
        {faculty?faculty.map((p,key)=>{
            return(
                <div className="container-fluid" key={key} style={{backgroundColor:"rgba(241, 241, 235, 0.705)",padding:"15px 0px ", marginBottom:"10px"}}>
                    <div className="row">
                        <div className="col-3" style={{textAlign: 'center'}}>
                        <h6 style={{fontWeight:"bold", color:"#2D3E4F"}}>{p.name}</h6>
                        </div>
                        <div className="col-4" style={{textAlign: 'center',marginTop:""}}>
                           <h6 style={{fontWeight:"bold", color:"#2D3E4F"}}>{p.email}</h6>
                        </div>
                        <div className="col-2" style={{textAlign: 'center',marginTop:""}}>
                        <h6 style={{fontWeight:"bold", color:"#2D3E4F"}}>{p.number}</h6>
                        </div>
                        <div className="col-3" style={{textAlign: 'center',marginTop:""}}>
                        <h6 style={{fontWeight:"bold", color:"#2D3E4F"}}>{p.aadhar}</h6>
                        </div>
                    </div>
                </div>
            )
        }):''}
      </TabPanel>
    </div>
  );
}