import React, { useState,useEffect } from 'react';
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import axios from 'axios';
import { isAuth,getCookie, authenticate } from '../../auth/window';
import CancelIcon from '@material-ui/icons/Cancel';
import { Button, Modal, ModalHeader, ModalBody, ModalFooter } from 'reactstrap';
import UserModal from './UserModal'
import ResultModal from './ResultModal'
import UserTimeline from './UserTimeline'


function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`scrollable-auto-tabpanel-${index}`}
      aria-labelledby={`scrollable-auto-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box p={3}>
          <Typography>{children}</Typography>
        </Box>
      )}
    </div>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.any.isRequired,
  value: PropTypes.any.isRequired,
};

function a11yProps(index) {
  return {
    id: `scrollable-auto-tab-${index}`,
    'aria-controls': `scrollable-auto-tabpanel-${index}`,
  };
}
const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
    backgroundColor: theme.palette.background.paper,
  },
}));

export default function UserTab({quizes,upcomingQuizes,timelineQuizes,oldQuizes}) {
  const classes = useStyles();
  const [value, setValue] = React.useState(0);

  const userId = isAuth().id
  const token = getCookie('token')

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

 
 

  return (
    <div className={classes.root}>
      <AppBar position="static">
        <Tabs 
        scrollButtons="auto"
        variant="scrollable"

        value={value} onChange={handleChange} aria-label="simple tabs example" style={{backgroundColor:"#2D3E4F"}}>
          <Tab className='vTab vTab2' label="Timeline" {...a11yProps(0)} />
          <Tab className='vTab vTab2' label="Quiz" {...a11yProps(1)} />
          <Tab className='vTab vTab2' label="Results" {...a11yProps(2)} />
         

        </Tabs>
      </AppBar>

      <TabPanel value={value} index={0}>
        <UserTimeline quizes={quizes} upcomingQuizes={upcomingQuizes} timelineQuizes={timelineQuizes} />
      </TabPanel>
     
      <TabPanel  value={value} index={1} style={{backgroundColor:"#fff"}}>
        {upcomingQuizes.length>0?upcomingQuizes.map((q,key)=>{
          return(
            <div className="container-fluid " key={key} style={{backgroundColor:"#f5ebec",padding:"15px 0px ", marginBottom:"10px",borderLeft:"5px solid #cf404d"}}>
                    <div className="row m-0">
                        <div className="col-sm-4 offset-1" style={{textAlign: 'left',padding:"5px",fontSize:"22px",fontWeight:"600",color:"#cf404d"}}>
                          <p className="m-0">{q.name}</p>
                          <p style={{fontSize:"15px",color:"black",margin:"0",marginLeft:"0px"}}>Faculty Name:- {q.author_name}</p>
                        </div>
                        <div className="col-sm-6" style={{textAlign: 'right',padding:"5px"}}>
                          <button  className="" style={{padding:"",borderRadius:"200px",border:"0px"}}>
                          <UserModal q={q}  />
                          </button>
                        </div>
                    </div>
            </div>
          )
        }) 
        :""}
       
      </TabPanel>
     

      <TabPanel  value={value} index={2}>
      {oldQuizes.length>0?oldQuizes.map((q,key)=>{
          return(
            <div className="container-fluid " key={key} style={{backgroundColor:"#f5ebec",padding:"15px 0px ", marginBottom:"10px",borderLeft:"5px solid #cf404d"}}>
                    <div className="row m-0">
                        <div className="col-sm-4 offset-1" style={{textAlign: 'left',padding:"5px",fontSize:"22px",fontWeight:"600",color:"#cf404d"}}>
                          <p className="m-0">{q.name}</p>
                          <p style={{fontSize:"15px",color:"black",margin:"0",marginLeft:"0px"}}>Faculty Name:- {q.author_name}</p>
                        </div>
                        <div className="col-sm-6" style={{textAlign: 'right',padding:"5px"}}>
                          <button style={{borderRadius:"200px",border:"0px"}}>
                          <ResultModal q={q}/>
                          </button>
                        </div>
                    </div>
            </div>
          )
        }) 
        :""}
      </TabPanel>
      
    </div>
  );
}
