import React, { useState,useEffect }from 'react';
import PropTypes from 'prop-types';
import Rating from '@material-ui/lab/Rating';
import SentimentVeryDissatisfiedIcon from '@material-ui/icons/SentimentVeryDissatisfied';
import SentimentDissatisfiedIcon from '@material-ui/icons/SentimentDissatisfied';
import SentimentSatisfiedIcon from '@material-ui/icons/SentimentSatisfied';
import SentimentSatisfiedAltIcon from '@material-ui/icons/SentimentSatisfiedAltOutlined';
import SentimentVerySatisfiedIcon from '@material-ui/icons/SentimentVerySatisfied';
import Box from '@material-ui/core/Box';
import TextField from '@material-ui/core/TextField';
import {useParams,useHistory} from 'react-router-dom'


const customIcons = {
  1: {
    icon: <SentimentVeryDissatisfiedIcon
    className="ratingIcon" />,
    label: 'Very Dissatisfied',
  },
  2: {
    icon: <SentimentDissatisfiedIcon 
    className="ratingIcon" />,
    label: 'Dissatisfied',
  },
  3: {
    icon: <SentimentSatisfiedIcon  className="ratingIcon" />,
    label: 'Neutral',
  },
  4: {
    icon: <SentimentSatisfiedAltIcon  className="ratingIcon" />,
    label: 'Satisfied',
  },
  5: {
    icon: <SentimentVerySatisfiedIcon  className="ratingIcon" />,
    label: 'Very Satisfied',
  },
};

function IconContainer(props) {
  const { value, ...other } = props;
  return <span {...other}>{customIcons[value].icon}</span>;
}

IconContainer.propTypes = {
  value: PropTypes.number.isRequired,
};

export default function UserRating() {
    const[rating,setRating] =useState(0)
    const [feedback,setFeedback]=useState('')

    const history =useHistory()

    const submit =() => {
      history.push('/dashboard')
    }

  return (
    <div className="container-fluid pt-4" >
        <h4>Please give a feedback</h4>
      <Box component="fieldset" mb={3} borderColor="transparent">
        <Rating
          style={{margin:"0px"}}
          name="customized-icons"
        value={rating}
        onChange={(event, newValue) => {
          setRating(newValue);
        }}
          defaultValue={rating}
          getLabelText={(value) => customIcons[value].label}
          IconContainerComponent={IconContainer}
        />
      </Box>
      <div className="col-md-10" style={{margin:"auto"}}>
      <TextField
          id="outlined-multiline-static"
          label="Feedback"
          multiline
          rows={6}
          variant="outlined"
          value={feedback}
          onChange={(e)=>setFeedback(e.target.value)}
          style={{width:"100%",marginTop:"-25px"}}

        />
        </div>
        <div class="wrap">
        <button 
        onClick={submit}
        disabled={rating===0 || feedback===''}
        className={rating===0 || feedback===''? ` submitbutton  mt-4 disable` :`submitbutton mt-4 `} 
        > Submit </button>
    </div>
    </div>
  );
}
