import React,{ useState,useEffect} from 'react';
import QuestionStepper from '../../components/User/QuestionStepper';
import {useParams,useHistory} from 'react-router-dom'
import axios from 'axios';
import {getCookie,setLocalStorage,getLocalStorage,removeLocalStorage} from '../../auth/window';

const StartQuiz =() =>{
    
    const [quiz, setQuiz]=useState('')
    const [questions, setQuestions]=useState([])
    const {id,resultId} = useParams()
    const token=getCookie('token')
    const history =useHistory()
    const [isVisible, setIsVisible] = useState(false);
    const [switchTab,setSwitchTab] = useState(3)
    

    const onVisibilityChange = () => setIsVisible(document.hidden);

    useEffect(() => {
        if(!getLocalStorage(id)){
            history.push("/dashboard")
        }
        if(getLocalStorage('warning')==null){
            setLocalStorage('warning',switchTab)
        }
            setSwitchTab(getLocalStorage('warning'))
        
        viewQuiz()
        viewQuestion()
        
    },[])

    useEffect(() => {
        window.addEventListener("visibilitychange", onVisibilityChange);
        document.title = isVisible;
        if(switchTab<0){
            removeLocalStorage(resultId)
            removeLocalStorage(id)
            removeLocalStorage('warning')

            history.push(`/finishquiz/${id}`)
        }
      
        return () => {
            if(isVisible){
                
                setLocalStorage('warning',switchTab-1)
                if(switchTab <= 0){
                    alert(`You have exceeded the maximum warning limit...`)
                }
                else {
                    alert(`You have ${switchTab-1} waring left after that your quiz is submitted automaticly.`);
               
                }
                setSwitchTab(switchTab-1);
        }
          window.removeEventListener("visibilitychange", onVisibilityChange);
        };
      });

    const viewQuiz =()=>{
        axios.get(`${process.env.REACT_APP_API}/quiz/${id}`,{
            headers: {
                'Content-Type': 'application/json',
                "Authorization":`Bearer ${token}`
            }
        })
        .then(res=>{
            setQuiz(res.data.Quiz[0])
        })
        .catch(err=>{console.log("error",err)})
    }
    const viewQuestion =()=>{
        axios.get(`${process.env.REACT_APP_API}/question-by-quiz/${id}`,{
            headers: {
                'Content-Type': 'application/json',
                "Authorization":`Bearer ${token}`
            }
        })
        .then(res=>{
            setQuestions(res.data.q)
            console.log(res.data.q)
        })
        .catch(err=>{console.log("error",err)})
    }

    return(
    <div className="container-fluid" style={{backgroundColor:"#d4e0ec",position:"fixed",height:"100%",overflow:"auto"}}>
        <div className="row">
            <div className="col-md-10 p-0 pt-4 mt-4" style={{margin:"auto",color:"#2d3e4f",textAlign:"center"}}>
                {quiz?quiz.length:""}
               <QuestionStepper questions={questions} quiz={quiz}/>
               
            </div>
        </div>
    </div>
    )}

export default StartQuiz;