import React,{ useState,useEffect} from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import CloseIcon from '@material-ui/icons/Close';
import Slide from '@material-ui/core/Slide';
import axios from 'axios';
import {getCookie,isAuth,setLocalStorage,getLocalStorage} from '../../auth/window';

import {useHistory} from 'react-router-dom'

const useStyles = makeStyles((theme) => ({
  appBar: {
    position: 'relative',
  },
  title: {
    marginLeft: theme.spacing(2),
    flex: 1,
  },
}));

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />;
});

export default function FullScreenDialog({q,name="VIEW"}) {
  const classes = useStyles();
  const [oneTime,setOneTime] = useState('false')
  const [open, setOpen] = React.useState(false);
  const token=getCookie('token')
  const history = useHistory()
  let date=new Date();
  let startTime=new Date(q.startTime)
  let diffMin=Math.ceil(( date-startTime) / (1000 * 60));
  let userId = isAuth().id;
  let quizStart=getLocalStorage(q._id)


  

  const oneTimeAccess=()=>{
    axios.get(`${process.env.REACT_APP_API}/result-quiz-student/${q._id}/${userId}`,{
      headers:{
        Authorization:`Bearer ${token}`,
      }
    })
    .then(res=>setOneTime(res.data.message))
    .catch(err=>console.log(err))
  }

  const handleClickOpen = () => {
    setOpen(true);
    oneTimeAccess()
  };

  const handleClose = () => {
    setOpen(false);
  };

  const handleStart = async (id) =>{
    
    axios.post(`${process.env.REACT_APP_API}/create-result`,{
      quizId:id, 
      userId
    },{
      headers: {
          'Content-Type': 'application/json',
          "Authorization":`Bearer ${token}`
      }
  })
  .then(res=>{
    handleClose()
    let todayDate = new Date()
    setLocalStorage(id,Date.parse(todayDate))
      history.push(`/quiz/${id}/${res.data.resultId}`)
  })
  .catch(err=>{console.log("error",err)})
  }

  const handleResume =(id) =>{
    axios.get(`${process.env.REACT_APP_API}/result-quiz-student/${q._id}/${userId}`,{
      headers: {
        'Content-Type': 'application/json',
        Authorization: `Bearer ${token}`
      }
    }).then(res => {
      let resultId = res.data.result._id
      console.log(resultId)
      history.push(`/quiz/${id}/${resultId}`)
      handleClose()
    })
      
    .catch(err => console.log(err))

  }


  return (
    <div>
       <Button variant="outlined" 
       style={{padding:"10px 30px",fontSize:"18px",borderRadius:"200px",border:"0px",textTransform:"none"}}
     onClick={handleClickOpen}>
        {name}

      </Button>
      <Dialog fullScreen open={open} onClose={handleClose} TransitionComponent={Transition}>
        <AppBar className={classes.appBar} style={{backgroundColor:"#cf404d"}}>
          <Toolbar>
            <IconButton edge="start" color="inherit" onClick={handleClose} aria-label="close">
              <CloseIcon />
            </IconButton>
            <Typography variant="h6" className={classes.title}>
              {q.name}
            </Typography>
            {quizStart && diffMin<q.duration && diffMin>0 ?
            <Button autoFocus color="inherit"
            style={{fontSize:"18px",fontWeight:"700"}}
            onClick={()=>handleResume(q._id)}
            >
              Resume
              
            </Button>
            :
            oneTime=="false"?
            <Button autoFocus color="inherit"
            style={{fontSize:"18px",fontWeight:"700"}}
            disabled={diffMin>=30000 ||diffMin>0}
            onClick={e=>handleStart(q._id)}
            >
              Start
            </Button>:"Submitted"
            }
          </Toolbar>
        </AppBar>
       <div className="container-fluid userModal p-0" style={{height:""}}>
         <div className="row m-0">
           <div className="col-md-10 p-0" style={{margin:"auto"}}>
             <div className="container-fluid p-0">
                <div className="row m-0">
                  <div className="col-md-12 pt-4" style={{textAlign: 'center',fontSize:"30px",fontWeight:"bold",color:"#cf404d"}}>
                      Quiz Name: {q.name}
                      <p style={{fontSize:"20px",color:"black",fontWeight:"500"}}>Faculty: {q.author_name}</p>
                  </div>
                  <div className="col-6">
                    <p style={{fontSize:"18px",fontWeight:"500", padding:" 0px", fontFamily:"cursive",marginBottom:"5px"}}>Total Question: {q.no_of_questions}</p>
                    <p style={{fontSize:"18px",fontWeight:"500", padding:" 0px", fontFamily:"cursive"}}>Duration: {q.duration} minutes</p>
                    </div>
                
                  <div className="col-6" style={{textAlign:"right"}}>
                  <p style={{fontSize:"18px",fontWeight:"500", padding:" 0px", fontFamily:"cursive",marginBottom:"5px"}}>Start Date: {q.startTime? q.startTime.split('T')[0]:""}</p>
                  <p style={{fontSize:"18px",fontWeight:"500", padding:" 0px", fontFamily:"cursive",marginRight:"51px"}}>Start Time: {q.startTime?q.startTime.split('T')[1]:""}</p>    
                  </div>
                  <div className="col-md-10 p-4 mt-4 mb-4" style={{marginTop:"20px", width:"100%", margin:"auto", backgroundColor:"#2d3e4f", color:"#fff"}}>
                    <h3>Instructions before starting Quiz</h3>
                      <ol style={{fontSize:"16px",marginTop:"10px"}}>
                      <li>Tab switching is not allowed after you start the quiz.</li>
                        <li>It is advised to give the exam on computer or laptop.</li>
                        <li>Quiz window will be of 5 minutes from start time.</li>
                        <li>You need to start the quiz within the available window provided by faculty.</li>
                        <li>After window is expired you will not be allowed to attempt the quiz.</li>
                        <li>After the duration alloted by faculty quiz will be automatically submitted.</li>
                        <li>To finish the quiz you can click on finish button.</li>                         
                      </ol>
                  </div>
                  </div>
                
                </div>
           </div>
         </div>
         
       </div>
      </Dialog>
    </div>
  );
}