import React from 'react';
import Layout from '../../components/Layout'
import { Col } from 'reactstrap';
import { useEffect } from 'react';
import Axios from 'axios';
import {useHistory,useParams} from 'react-router-dom'
import { toast } from "react-toastify";

const VerifyAccount= ()=> {
    const history=useHistory()
    const {token,email}=useParams()

    useEffect(()=>{
        if(email && token){
        Axios.put(`${process.env.REACT_APP_API}/register`, {},{
            headers:{
                "Content-Type":'application/json',
                Authorization:`Bearer ${token}`
                }
        }).then(data=>{
               if(data.error){
               console.log(data.error)
               }
               else{
                window.localStorage.setItem('email',email);   

                    setTimeout(()=>{history.push('/updateprofile')},5000)
                }
            }).catch(err=>{
                console.log(err)
            })
        }
    },[history,email,token])

    return (
        <div style={{background:`url("https://res.cloudinary.com/quizee/image/upload/v1625901107/Profile/ubfowehmrnl4dceymxke.png")`
       ,  backgroundSize:"125px 80px",height:"100%",position:"fixed",width:"100%"}}>

            <div className="backsignin1">
            <div style={{paddingBottom:"50px",marginTop:"10px"}}>
                <h3>You are verified successfully!</h3>
                <br/>
                <h4>you will be redirected to updated profile page shortly...</h4>
            </div>
            </div>
            </div>
           
        
       
    )
  }
  export default VerifyAccount;