import React,{ useState,useEffect} from 'react';
import { Modal, ModalBody } from 'reactstrap';
import { Chart } from "react-google-charts";

const MyModal = (toggle,modal,setModal) => {

    return (
        <>
            <Modal isOpen={modal} toggle={toggle} className="my-modal mb-4 "  size="lg"
                centered={true}
                style={{textAlign: 'center', maxWidth:"600px" ,zIndex:"100000"}}
                >
                  <Chart
  width={'500px'}
  height={'300px'}
  chartType="PieChart"
  loader={<div>Loading Chart</div>}
  data={[
    ['Task', 'Hours per Day'],
    ['Work', 11],
    ['Eat', 2],
    ['Commute', 2],
    ['Watch TV', 2],
    ['Sleep', 7],
  ]}
  options={{
    title: 'My Daily Activities',
    // Just add this option
    is3D: true,
  }}
  rootProps={{ 'data-testid': '2' }}
/>
</Modal>
        </>
    )
}

export default MyModal;