import React , { useState,useEffect }from 'react';
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/core/styles';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import axios from 'axios';
import { isAuth,getCookie, authenticate } from '../../auth/window';
import { Link } from 'react-router-dom';
import MyInput from '../MyInput'
import CancelIcon from '@material-ui/icons/Cancel';
import { Button, Modal, ModalHeader, ModalBody, ModalFooter } from 'reactstrap';

function TabPanel(props) {
  const { children, value, index, ...other } = props;


  return (
    <div
        style={{ width: `80%`, padding:`0px 0px 0px 10px`}}
      role="tabpanel"
      hidden={value !== index}
      id={`vertical-tabpanel-${index}`}
      aria-labelledby={`vertical-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box p={3}>
          <Typography>{children}</Typography>
        </Box>
      )}
    </div>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.any.isRequired,
  value: PropTypes.any.isRequired,
};

function a11yProps(index) {
  return {
    id: `vertical-tab-${index}`,
    'aria-controls': `vertical-tabpanel-${index}`,
  };
}

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
    backgroundColor: theme.palette.background.paper,
    display: 'flex',

  },
  tabs: {
    borderRight: `1px solid ${theme.palette.divider}`,
    backgroundColor: ""
  },
}));

export default function VerticalTab2() {
  const classes = useStyles();
  const token=getCookie('token')
  const [value, setValue] = React.useState(0);
  const [viewAdmin, setViewAdmin] = React.useState();
  const [email, setEmail] = React.useState();
  const [emailid, setEmailid] = React.useState('');
  const [message,setMessage]=useState('')
  const [error,setError]=useState('')
  const [modal, setModal] = useState(false);

  const toggle = () => setModal(!modal);

  const toggle1 = (mailid) => {
    setEmailid(mailid);
    toggle();
  }

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  const handleView=async()=>{

      await axios.get(`${process.env.REACT_APP_API}/all-faculty`,{
        headers:{
            "Content-Type": "application/json",
            Authorization: `Bearer ${token}`
        }
      })
      .then(res=>{
        setViewAdmin(res.data.users)
      })
      .catch(e=>console.log(e))
  }

  
  const handleSubmit = (e) => {
    e.preventDefault();
    if(!email){
        setError('Please enter Email id')
        setMessage('')
    }
    else if(!/^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/.test(email)){
        setError('Please Enter valid Email id')
        setMessage('')
    }
    else{

    axios.post(`${process.env.REACT_APP_API}/add-new-faculty`,{email},{
        headers:{
            "Content-Type":'application/json',
            authorization: `Bearer ${token}`
            }   
    })
    .then(res=>{
        if(res.data){
        setError('')
        setMessage(`email has been sent successfully to ${email}`)
        setEmail('')
        }
        else if(res.error){
            setError(res.error,"error")
            setMessage('')
        }
        
    }).catch(err=>{
        setError(`email already exists try to login`)
        setMessage('')
    })

    } 
  }

  const handleDelete = (e) =>{
    e.preventDefault();
    const data={
      email:emailid
    }
    axios.delete(`${process.env.REACT_APP_API}/remove-user/${emailid}`,{
      headers:{
          "Content-Type":'application/json',
          "Authorization": `Bearer ${token}`
          }   
  })
  .then(res=>{
      if(res.data){
      toggle()
      setError('')
      setMessage(``)
      setEmailid('')
      handleView()
      }
      else if(res.error){
          setError(res.error,"error")
          setMessage('')
      }
      
  }).catch(err=>{
      setError(`err`, err)
      setMessage('')
      console.log(err, 'hello')
  })
    
  }

  return (
    <div className={classes.root}>
      <Tabs
        orientation="vertical"
        value={value}
        onChange={handleChange}
        aria-label="Vertical tabs example"
        className={classes.tabs}
      >
        <Tab className='vTab vTab1' label="Add New Faculty" {...a11yProps(0)} />
        <Tab className='vTab vTab1' onClick={e=>handleView()} label="View All Faculty" {...a11yProps(1)} />
        
    
      </Tabs>
      <TabPanel  value={value} index={0} >
      {error && error.length?<div className="alert alert-danger">{error}</div>:""}
      {message && message.length?<div className="alert alert-info">{message}</div> : ''}
      <MyInput label={"E-Mail"} type={"email"} value={email} setValue={setEmail}/>
      <br/>
      <Link to="/signin"><button className="homebtn signinbtn" onClick={e=>handleSubmit(e)}>Add New Faculty</button></Link>
      </TabPanel>
      <TabPanel value={value} index={1}>
      <div className="container-fluid"  style={{backgroundColor:"#cf404d",padding:" 0px ", marginBottom:"10px"}}>
                    <div className="row">
                    <div className="col-2" style={{textAlign: 'center',marginTop:"8px"}}>
                           <h5 style={{fontWeight:"bold", color:"white"}}>Image</h5>
                        </div>
                        <div className="col-3" style={{textAlign: 'center',marginTop:"8px"}}>
                           <h5 style={{fontWeight:"bold", color:"white"}}>Name</h5>
                        </div>
                        <div className="col-5" style={{textAlign: 'center',marginTop:"8px"}}>
                        <h5 style={{fontWeight:"bold", color:"White"}}>Email</h5>
                        </div>
                        <div className="col-2" style={{textAlign: 'center',marginTop:"8px"}}>
                        <h5 style={{fontWeight:"bold", color:"White"}}>Remove</h5>
                        </div>
                    </div>
                </div>
        {viewAdmin?viewAdmin.map((p,key)=>{
            return(
                <div className="container-fluid" key={key} style={{backgroundColor:"rgba(241, 241, 235, 0.705)",padding:"15px 0px ", marginBottom:"10px"}}>
                    <div className="row">
                        <div className="col-2" style={{textAlign: 'center'}}>
                            <img
                            style={{ width:"40px", height:"40px", borderRadius:"100%"}} 
                            src={p.pic}/>
                        </div>
                        <div className="col-3" style={{textAlign: 'center',marginTop:"8px"}}>
                           <h6 style={{fontWeight:"bold", color:"#2D3E4F"}}>{p.name}</h6>
                        </div>
                        <div className="col-5" style={{textAlign: 'center',marginTop:"8px"}}>
                        <h6 style={{fontWeight:"bold", color:"#2D3E4F"}}>{p.email}</h6>
                        </div>
                        <div className="col-2" style={{textAlign: 'center',marginTop:"8px"}}>
                        
                        <CancelIcon
                        onClick={e=>toggle1(p.email)}
                      style={{
                        textAlign: 'center',
                        fontSize:"30px", 
                        color: '#cf404d',
                        margin:"auto",
                        cursor: 'pointer'}} 
                      />
                        </div>
                    </div>
                </div>
            )
        }):''}
      </TabPanel>

      <div>
      <Modal isOpen={modal} toggle={toggle} className="my-modal mb-4 "  size="lg"
                centered={true}
                style={{textAlign: 'center', maxWidth:"600px" }}
                >
                <ModalBody >
                    <div className="mt-2 mb-4" style={{fontSize:"30px"}}> Are you sure you want to delete {emailid}? </div>
                    
                </ModalBody>
                <div style={{textAlign: 'center',paddingBottom:"20px"}}> 
                  <Button color="homebtn profilebtn"
                  style={{fontSize:"20px", padding:"5px 20px", fontFamily:"cursive"}}
                  onClick={e=>handleDelete(e)}>
                      Yes</Button>{'   '}
                  <Button
                  style={{fontSize:"20px", padding:"5px 20px", fontFamily:"cursive"}} 
                  color="homebtn profilebtn2" onClick={toggle}>No</Button>
                  </div>
              </Modal>
    </div>

    </div>

  );
}