import { useState } from 'react';
import styles from '../css/MyInput.css'

const MyInput=({label,type,value,setValue,clas="j" })=> {
  const [isActive, setIsActive] = useState(false);
  
  const handleTextChange=(text)=> {
    setValue(text);
    if (text !== '') {
      setIsActive(true);
    } else {
      setIsActive(false);
    }
  }
    return (
            <div className="float-label ">
            <input
            style={{overflow:"auto"}}
            type={type}
            value={value}
            onChange={(e) => handleTextChange(e.target.value)}
            />
            <label className={ isActive ? `Active ${clas}`  : `${clas}`} htmlFor={type} >
                {label}
            </label>
        </div>
    )
  }
  
  export default MyInput;