import React from 'react';
import {Link} from 'react-router-dom'
import styles from '../css/MyCard.css'

import {
    Card, CardText, CardBody,
    CardTitle
  } from 'reactstrap';

function MyCard({icon,cardlink='#',cardtext,cardtitle}) {
    return (
        <Link to={cardlink}>
            <Card className="cardabout">
                <div className="aboutIcons">
                    <i className="aboutIcon material-icons">{icon}</i>
                </div>
            
            <CardBody className="cardBody">
                <CardTitle className="aboutTitle" tag="h5">{cardtitle}</CardTitle>

                <CardText>{cardtext}</CardText>
                </CardBody>
            </Card> 
            </Link>
    )
  }
  
  export default MyCard;