import React, {useState} from 'react'
import { useEffect } from 'react';
import { Chart } from "react-google-charts";
import { PDFDownloadLink,PDFViewer } from "@react-pdf/renderer";
import ResultPdf from './ResultPdf'

const FacultyResult = ({result}) => {
    const [marks, setMarks] = useState([])
    let temp=[]
    var data = [];
      
    useEffect(()=>{

        if(result&&result.length>0)
        {
            let reslen =  result[0].questions.length
        for(let i=0; i<=reslen; i++) {
            temp[i]=[]
            temp[i][0]=`${i} marks`;
            temp[i][1]=0;
        }
        for(let i=0;i<result.length;i++){
            temp[result[i].marks][1]+=1
        }
        data.push(["result","analysis"])
        for(let i=0; i<=reslen; i++) {
            data.push(temp[i])
        }
        setMarks(data)
        }
    },[result])
    
    
    return (
        <div className="container-fluid" style={{color:"black"}}>
            <div className="row">
        
                <div className="col-md-10 p-4" style={{margin:"auto"}}>
                 {marks.length>1? 
                  <Chart
                  
                  chartType="PieChart"
                  loader={<div>Loading Chart</div>}
                  style={{margin:"auto",maxWidth:"100%",maxHeight:"100%",width:"600px",height:"400px"}}
                  data={marks}
                  options={{
                    title: 'Result Analysis',
                    // Just add this option
                    is3D: true,
                  }}
                  rootProps={{ 'data-testid': '2' }}
                />:"No Student have attempted the quiz till now"}



                <PDFDownloadLink
      document={result?<ResultPdf result={result}/>:""}
      fileName={`${result[0].quiz.name}.pdf`}
      className="btn btn-sm btn-block btn-outline-primary"
    >
      Download Result PDF
    </PDFDownloadLink>
    
                  
                
                </div>
            </div>
        </div>
    )
}

export default FacultyResult;