import Layout from '../../components/Layout'
import MyInput from '../../components/MyInput'
import { Col } from 'reactstrap';
import React,{ useEffect, useState } from 'react';
import axios from 'axios';
import {Link,useHistory} from 'react-router-dom'
import { authenticate ,removeLocalStorage} from '../../auth/window';
import { toast } from "react-toastify";

const Updateprofile=()=> {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [name, setName] = useState('');
  const [number, setNumber] = useState('');
  const [dob, setDob] = useState(new Date());

  const history = useHistory()

  useEffect(() => {
    setEmail(window.localStorage.getItem("email"));   
  }, []);

  const handleSubmit =(e) =>{
    e.preventDefault();
    axios.put(`${process.env.REACT_APP_API}/profile`,{
      email,
      name,
      password,
      mobile_number:number,
      dob
    },{
      header:{
        "content-type":"application/json"
      }
    })
    .then(res=>{
      const data={
        token:res.data.d.signinToken,
        user:{
          role:res.data.d.role,
          email:res.data.d.email,
          name:res.data.d.name,
          number:res.data.d.mobile_number,
          dob:res.data.d.dob,
          pic:res.data.d.pic,
          id:res.data.d._id,
        }
      }
      
      authenticate(data)
      removeLocalStorage('email')
      setEmail('')
      setName('')
      setDob(new Date())
      setPassword('')
      setNumber('')
      toast.success('Sign in Successfully', {
        position: "top-center",
        });
        if(res.data.d.role ==='19' || res.data.d.role ==='0'){
          history.push('/admindashboard')
        }
        else if(res.data.d.role ==='1'){
          history.push('/facultydashboard')
        }
        else{
          history.push('/dashboard')
        }
      
      
    })
    .catch(err=>console.log(err))
  }
  
    return (
        <div style={{background:`url("https://res.cloudinary.com/quizee/image/upload/v1625901107/Profile/ubfowehmrnl4dceymxke.png")`
       ,  backgroundSize:"125px 80px",height:"100%",position:"fixed",width:"100%"}}>

            <div className="animate__animated animate__pulse backsignin1" style={{marginTop:"8%"}}>
            <h1>Profile</h1>
            <input type="email" className="form-control" 
            value={email} disabled />
            <MyInput label={"Name"} type={"text"} value={name} setValue={setName}/>
            <MyInput label={"Password"} type={"password"} value={password} setValue={setPassword}/>
            <MyInput label={"Mobile Number"} type={"text"} value={number} setValue={setNumber}/>
            <MyInput label={"DOB"} type={"date"} value={dob} setValue={setDob} clas={'Active'}/>
            <br/>
            <Link to="/signin"><button className="homebtn signinbtn" onClick={e=>handleSubmit(e)}>Submit</button></Link>
           
            
            </div>
            </div>
        

       
    )
  }
  
  export default Updateprofile;