import React, { useState,useEffect } from 'react';
import {Link} from 'react-router-dom'
import Layout from '../components/Layout'
import { Col } from 'reactstrap';
import styles from '../css/About.css'
import MyInput from '../components/MyInput2'
import moment from 'moment'
import { Button, Modal, ModalHeader, ModalBody, ModalFooter } from 'reactstrap';
import { isAuth,getCookie, authenticate } from '../auth/window';
import axios from 'axios';
import { toast } from "react-toastify";

const Profile=()=>{
    const token=getCookie('token')
    const [modal, setModal] = useState(false);
    const [modal1, setModal1] = useState(false);
    const [password, setPassword] = useState('');
    const [confirmPassword, setConfirmPassword] = useState('');
    const [name, setName] = useState('');
    const [number, setNumber] = useState('');
    const [email, setEmail] = useState('');
    const [dob, setDob] = useState('');
    const [error, setError] = useState("");

    const [image,setImage]=useState("")
    const [pic,setPic]=useState("")

    const updateprofile = (e) =>{
        e.preventDefault();
        if(!name || !number || !dob){
            setError("Please enter all fields")
            return;
        }
        if(isNaN(number)){
            setError("Please enter valid number")
            return 
        }
    axios.put(`${process.env.REACT_APP_API}/update-profile`,{name,mobile_number:number,dob},{
        headers:{
            "Content-Type": "application/json",
            authorization: `Bearer ${token}`
        }
    })
    .then(res=>{
        setError("")
        const data={
          token,
          user:{
            role:res.data.d.role,
            email:res.data.d.email,
            name:res.data.d.name,
            number:res.data.d.mobile_number,
            dob:res.data.d.dob,
            pic:res.data.d.pic,
            id:res.data.d._id
          }
        }
        authenticate(data)
        toast.success('Updated Successfully', {
          position: "top-center",
          });
        toggle()
      })
      .catch(err=>console.log(err))
    }

    const handlePassword=(e)=>{
        e.preventDefault();
        if(!password || !confirmPassword){
            setError("Please enter password in both the fields")
            return;
        }
        if(password!==confirmPassword){
            setError("Password is not matched")
            return;           
        }
    axios.put(`${process.env.REACT_APP_API}/update-password`,{password},{
        headers:{
            "Content-Type": "application/json",
            authorization: `Bearer ${token}`
        }
    })
    .then(res=>{
        setError("")
        toast.success('Password Updated Successfully', {
          position: "top-center",
          });
          setPassword("")
          setConfirmPassword()
        toggle1()

      })
      .catch(err=>console.log(err))
    }

    const toggle = (e) => {
        setModal(!modal);

    }
    const toggle1 = () => setModal1(!modal1);

    useEffect(()=>{
        let user=isAuth()
        setName(user.name)
        setEmail(user.email)
        setDob(user.dob)
        setNumber(user.number)
        setPic(user.pic)
    },[])

    useEffect(async()=>{
        if(image){
            const data=new FormData()
            data.append('file',image)
            data.append("upload_preset",`${process.env.REACT_APP_CLOUD_UPLOAD_PRESET}`)
            data.append("cloud_name",`${process.env.REACT_APP_CLOUD_NAME}`)            
            fetch(`${process.env.REACT_APP_CLOUDNARY_API}`,{
                method:"POST",
                body:data
            })
            .then(res=>res.json())
            .then(data=>{
                setPic(data.url)
                axios.put(`${process.env.REACT_APP_API}/update-pic`,{pic:data.url},{
                    headers:{
                        "Content-Type": "application/json",
                        authorization: `Bearer ${token}`
                    }
                })
                .then(res=>{
                    setError("")
                    const data={
                      token,
                      user:{
                        role:res.data.d.role,
                        email:res.data.d.email,
                        name:res.data.d.name,
                        number:res.data.d.mobile_number,
                        dob:res.data.d.dob,
                        pic:res.data.d.pic
                      }
                    }
                    authenticate(data)
                    toast.success('Updated Successfully', {
                      position: "top-center",
                      });
                  })
                  .catch(err=>console.log(err))
            
            })
            .catch(err=>{
                console.log(err)
            })
        }
    },[image])

    


    return (
        <Layout>
            <Col md="9" className="back2">
            <div className="aboutText">
                <h1 className="abouth1">Profile</h1>                
            </div>
            <div className="container" style={{marginTop:"20px"}}>
                <div className="row">
                    <div className="col-md-4" style={{textAlign:"center"}}>
                        <img  
                        style={{borderRadius:"100%",height:"250px",width:"250px"}}
                        src={pic}/>
                        <label className="mt-3 homebtn signinbtn"

                        style={{textAlign:"center"}}
                        >
                        Change Photo
                        <input
                            type="file"
                            hidden
                            onChange={e=>setImage(e.target.files[0])}
                        />
                        </label>
                    </div>
                    <div className="col-md-7">
                    <MyInput label={"Name"} type={"text"} value={name} setValue={""} disable={true} clas={'Active'}/>
                    <MyInput label={"Email"} type={"text"} value={email} setValue={""} disable={true} clas={'Active'}/>
                    <MyInput label={"Mobile Number"} type={"text"} value={number} setValue={""} disable={true} clas={'Active'}/>
                    <MyInput label={"DOB"} type={"text"} value={moment(dob).format('L')} setValue={""} disable={true} clas={'Active'}/>
                    <MyInput label={"Password"} type={"password"} value={"fuckoffbitch"} setValue={""} disable={true} clas={'Active'}/>
                        <br/>
                    <div style={{textAlign: 'center'}}>   
                    <button onClick={toggle} className="homebtn profilebtn">Edit Profile</button>
                    <button onClick={toggle1} className="homebtn profilebtn2">Edit Password</button>
                    </div>
                    </div>
                        
                </div>
                <Modal isOpen={modal} toggle={toggle} className="my-modal mb-4 "  size="lg"
                centered={true}
                style={{textAlign: 'center', maxWidth:"600px" }}
                >
                <ModalBody >
                    <div className="mt-2 mb-4" style={{fontSize:"30px"}}> Edit Profile </div>
                    {error?<div className="alert alert-danger">{error}</div>:""}
                    <MyInput label={"Name"} type={"text"} value={name} setValue={setName}clas={'Active'}/>
                    <MyInput label={"Mobile Number"} type={"text"} value={number} setValue={setNumber}clas={'Active'}/>
                    <MyInput label={"DOB"} type={"date"} value={dob} setValue={setDob} clas={'Active'}/>


                </ModalBody>
                <div style={{textAlign: 'center',paddingBottom:"20px"}}> 
                  <Button color="homebtn profilebtn"
                  style={{fontSize:"20px", padding:"5px 20px", fontFamily:"cursive"}}
                  onClick={e=>updateprofile(e)}>
                      Save</Button>{'   '}
                  <Button
                  style={{fontSize:"20px", padding:"5px 20px", fontFamily:"cursive"}} 
                  color="homebtn profilebtn2" onClick={toggle}>Cancel</Button>
                  </div>
              </Modal>


              <Modal isOpen={modal1} toggle={toggle1} className="my-modal mb-4 "  size="lg"
                centered={true}
                style={{textAlign: 'center', maxWidth:"600px" }}
                >
                <ModalBody >
                    <div className="mt-2 mb-4" style={{fontSize:"30px"}}> Edit Password </div>
                    {error?<div className="alert alert-danger">{error}</div>:""}
                    <MyInput label={"New Password"} type={"password"} value={password} setValue={setPassword}/>
                    <MyInput label={"Confirm Password"} type={"password"} value={confirmPassword} setValue={setConfirmPassword}/>

                </ModalBody>
                <div style={{textAlign: 'center',paddingBottom:"20px"}}> 
                  <Button color="homebtn profilebtn" 
                  style={{fontSize:"20px", padding:"5px 20px", fontFamily:"cursive"}}
                  onClick={e=>handlePassword(e)}>Save</Button>{'   '}
                  <Button color="homebtn profilebtn2" 
                   style={{fontSize:"20px", padding:"5px 20px", fontFamily:"cursive"}}

                  onClick={toggle1}>Cancel</Button>
                  </div>
              </Modal>

            </div>
            </Col>
        </Layout>
    )
}

export default Profile;