import React , { useState, useEffect }from 'react';
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/core/styles';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import axios from 'axios';
import { isAuth,getCookie, authenticate } from '../../auth/window';
import MyInput from '../MyInput'
import { Link } from 'react-router-dom';

function TabPanel(props) {
  const { children, value, index, ...other } = props;


  return (
    <div
        style={{ width: `80%`, padding:`0px 0px 0px 10px`}}
      role="tabpanel"
      hidden={value !== index}
      id={`vertical-tabpanel-${index}`}
      aria-labelledby={`vertical-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box p={3}>
          <Typography>{children}</Typography>
        </Box>
      )}
    </div>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.any.isRequired,
  value: PropTypes.any.isRequired,
};

function a11yProps(index) {
  return {
    id: `vertical-tab-${index}`,
    'aria-controls': `vertical-tabpanel-${index}`,
  };
}

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
    backgroundColor: theme.palette.background.paper,
    display: 'flex',

  },
  tabs: {
    borderRight: `1px solid ${theme.palette.divider}`,
    backgroundColor: ""
  },
}));

export default function VerticalTab() {
  const classes = useStyles();
  const token=getCookie('token')
  const [value, setValue] = React.useState(0);
  const [viewAdmin, setViewAdmin] = React.useState();
  const [email, setEmail] = React.useState();
  const [message,setMessage]=useState('')
  const [error,setError]=useState('')

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  const handleView=async()=>{
      await axios.get(`${process.env.REACT_APP_API}/all-admin`,{
        headers:{
            "Content-Type": "application/json",
            Authorization: `Bearer ${token}`
        }
      })
      .then(res=>{
        setViewAdmin(res.data.users)
      })
      .catch(e=>console.log(e))
  }

  const handleSubmit = (e) => {
    e.preventDefault();
    if(!email){
        setError('Please enter Email id')
        setMessage('')
    }
    else if(!/^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/.test(email)){
        setError('Please Enter valid Email id')
        setMessage('')
    }
    else{

    axios.post(`${process.env.REACT_APP_API}/add-new-admin`,{email},{
        headers:{
            "Content-Type":'application/json',
            authorization: `Bearer ${token}`
            }   
    })
    .then(res=>{
        if(res.data){
        setError('')
        setMessage(`email has been sent successfully to ${email}`)
        setEmail('')
        }
        else if(res.error){
            setError(res.error,"error")
            setMessage('')
        }
        
    }).catch(err=>{
        setError(`email already exists try to login`)
        setMessage('')
        console.log(err, 'hello')
    })

    } 
  }

  return (
    <div className={classes.root}>
      <Tabs
        orientation="vertical"
        value={value}
        onChange={handleChange}
        aria-label="Vertical tabs example"
        className={classes.tabs}
      >
        <Tab className='vTab vTab1' label="Add New Admin" {...a11yProps(0)} />
        <Tab className='vTab vTab1' onClick={e=>handleView()} label="View All Admin" {...a11yProps(1)} />
        
      </Tabs>
      <TabPanel  value={value} index={0} >
      {error && error.length?<div className="alert alert-danger">{error}</div>:""}
      {message && message.length?<div className="alert alert-info">{message}</div> : ''}
      <MyInput label={"E-Mail"} type={"email"} value={email} setValue={setEmail}/>
      <br/>
      <Link to="/signin"><button className="homebtn signinbtn" onClick={e=>handleSubmit(e)}>Add New Admin</button></Link>
      </TabPanel>
      <TabPanel value={value} index={1}>
      <div className="container-fluid"  style={{backgroundColor:"#cf404d",padding:" 0px ", marginBottom:"10px"}}>
                    <div className="row">
                    <div className="col-2" style={{textAlign: 'center',marginTop:"8px"}}>
                           <h5 style={{fontWeight:"bold", color:"white"}}>Image</h5>
                        </div>
                        <div className="col-4" style={{textAlign: 'center',marginTop:"8px"}}>
                           <h5 style={{fontWeight:"bold", color:"white"}}>Name</h5>
                        </div>
                        <div className="col-6" style={{textAlign: 'center',marginTop:"8px"}}>
                        <h5 style={{fontWeight:"bold", color:"White"}}>Email</h5>
                        </div>
                    </div>
                </div>
        {viewAdmin?viewAdmin.map((p,key)=>{
            return(
                <div className="container-fluid" key={key} style={{backgroundColor:"rgba(241, 241, 235, 0.705)",padding:"15px 0px ", marginBottom:"10px"}}>
                    <div className="row">
                        <div className="col-2" style={{textAlign: 'center'}}>
                            <img
                            style={{ width:"40px", height:"40px", borderRadius:"100%"}} 
                            src={p.pic}/>
                        </div>
                        <div className="col-4" style={{textAlign: 'center',marginTop:"8px"}}>
                           <h5 style={{fontWeight:"bold", color:"#2D3E4F"}}>{p.name}</h5>
                        </div>
                        <div className="col-6" style={{textAlign: 'center',marginTop:"8px"}}>
                        <h5 style={{fontWeight:"bold", color:"#2D3E4F"}}>{p.email}</h5>
                        </div>
                    </div>
                </div>
            )
        }):''}
      </TabPanel>
    </div>
  );
}