import React from 'react';
import clsx from 'clsx';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import Drawer from '@material-ui/core/Drawer';
import CssBaseline from '@material-ui/core/CssBaseline';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import List from '@material-ui/core/List';
import Typography from '@material-ui/core/Typography';
import Divider from '@material-ui/core/Divider';
import IconButton from '@material-ui/core/IconButton';
import MenuIcon from '@material-ui/icons/Menu';
import ChevronLeftIcon from '@material-ui/icons/ChevronLeft';
import ChevronRightIcon from '@material-ui/icons/ChevronRight';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import InboxIcon from '@material-ui/icons/MoveToInbox';
import MailIcon from '@material-ui/icons/Mail';

import {Link, useHistory} from 'react-router-dom'
import { Row, Col } from 'reactstrap';
import styles from '../css/Layout.css'
import axios from 'axios'
import {isAuth, removeCookie,removeLocalStorage} from '../auth/window'
import { toast } from "react-toastify";
import Header from './Header';

const drawerWidth = 240;

const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
    BackgroundColor:'black',
  },
  appBar: {
    transition: theme.transitions.create(['margin', 'width'], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
  },
  appBarShift: {
    width: `calc(100% - ${drawerWidth}px)`,
    marginLeft: drawerWidth,
    transition: theme.transitions.create(['margin', 'width'], {
      easing: theme.transitions.easing.easeOut,
      duration: theme.transitions.duration.enteringScreen,
    }),
  },
  menuButton: {
    marginRight: theme.spacing(2),
  },
  hide: {
    display: 'none',
  },
  drawer: {
    width: drawerWidth,
    flexShrink: 0,
  },
  drawerPaper: {
    width: drawerWidth,
    backgroundColor:"#2D3E4F",
  },
  drawerHeader: {
    display: 'flex',
    alignItems: 'center',
    padding: theme.spacing(0, 1),
    // necessary for content to be below app bar
    ...theme.mixins.toolbar,
    justifyContent: 'flex-end',
  },
  content: {
    flexGrow: 1,
    padding: theme.spacing(3),
    transition: theme.transitions.create('margin', {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
    marginLeft: -drawerWidth,
  },
  contentShift: {
    transition: theme.transitions.create('margin', {
      easing: theme.transitions.easing.easeOut,
      duration: theme.transitions.duration.enteringScreen,
    }),
    marginLeft: 0,
  },
}));

export default function PersistentDrawerLeft() {
  const classes = useStyles();
  const theme = useTheme();
  const [open, setOpen] = React.useState(false);
  const history =useHistory()
  const signout =() =>{
      removeCookie('token');
      removeLocalStorage('user');
      axios.get(`${process.env.REACT_APP_API}/signout`,{},{
        headers: {}  
      }).then((res)=>{
          toast.success('Logout Successfully', {
              position: "top-center",
              });
      history.push("/signin")
      }).catch((err)=>console.log(err))
  }
  const handleDrawerOpen = () => {
    setOpen(true);
  };

  const handleDrawerClose = () => {
    setOpen(false);
  };

  return (
    <div className={classes.root} >
      <CssBaseline />
      <AppBar
        style={{backgroundColor:"#cf404d"}}
        position="fixed"
        className={clsx(classes.appBar, {
          [classes.appBarShift]: open,
        })}
      >
        <Toolbar>
          <IconButton
            style={{color:"#2D3E4F"}}
            aria-label="open drawer"
            onClick={handleDrawerOpen}
            edge="start"
            className={clsx(classes.menuButton, open && classes.hide)}
          >
            <MenuIcon />
          </IconButton>
          <Link to="/">
          <Typography  variant="h6" noWrap style={{fontFamily:"'Courier New', Courier, monospace",fontSize:"30px"}} >
            <img src="https://res.cloudinary.com/quizee/image/upload/v1625986617/Logo_blue_t_xzzizw.png"
            style={{maxHeight:"100px",maxWidth:"150px"}}/>
          </Typography>
          </Link>
        </Toolbar>
      </AppBar>
      <Drawer
        className={classes.drawer}
        variant="persistent"
        anchor="left"
        open={open}
        
        classes={{
          paper: classes.drawerPaper,
        }}
      >
        <div  className={classes.drawerHeader}>
          <IconButton onClick={handleDrawerClose}>
            {theme.direction === 'ltr' ? <ChevronLeftIcon /> : <ChevronRightIcon />}
          </IconButton>
        </div>
        
        <List>
        <div style={{textAlign:"center"}}>
                <Link to="/" ><h1 className="brandText mt-4"></h1></Link>
                {isAuth()?<h5 style={{color:"rgba(238, 240, 219, 0.705)",marginTop:"-15px",marginBottom:"40px"}} className="">Hi {isAuth().name}</h5>:""}
                <ul style={{listStyleType:"none",margin:"0px",padding:"0px"}}>
                <li><Link to="/" ><h5  className="navlink">Home</h5></Link></li>
                {
                    isAuth() ?
                    <li><Link to='/profile' ><h5  className="navlink">Profile</h5></Link></li>
                    :""
                }
                {!isAuth() ?(
                     <>
                     <li><Link to="/about"><h5 className="navlink">About</h5></Link></li>
                     <li><Link to="/contact"><h5  className="navlink">Contact us</h5></Link></li>
                     </>
                )
                :
                isAuth().role==='0' || isAuth().role==="19"?
                (
                    <>
                     <li><Link to="/admindashboard"><h5 className="navlink">Dashboard</h5></Link></li>
                     </>
                )
                :
                isAuth().role==='1' ?
                (
                    <>
                     <li><Link to="/facultydashboard"><h5 className="navlink">Dashboard</h5></Link></li>
                     </>
                ):
                (
                    <>
                    <li><Link to="/dashboard"><h5 className="navlink">Dashboard</h5></Link></li>
                    </>
                )
                }
                <li><Link to="/team"><h5  className="navlink">our team</h5></Link></li>

                {
                    isAuth() ?
                    <li><Link to='#' onClick={signout}><h5  className="navlink">Signout</h5></Link></li>
                    :""
                }
                </ul>
               
                </div>
        </List>
      </Drawer>
        
    </div>
  );
}
