import React, { useState }  from 'react';
import Layout from '../../components/Layout'
import { Col } from 'reactstrap';
import {Link} from 'react-router-dom'
import MyInput from '../../components/MyInput'
import axios from 'axios';

const ForgotPassword = () => {
    const [email, setEmail] = useState('');
    const [message,setMessage]=useState('')
    const [error,setError]=useState('')
  
    const handleSubmit=(e)=>{
      e.preventDefault();
      if(!email){
        setError("Please enter email")
        setMessage('')
        return;
      }
      axios.put(`${process.env.REACT_APP_API}/forgotpassword`,{email},{
        headers:{
          "Content-Type": "application/json"
        }
      })
      .then(res =>{
        if(res.data.error){
          setError(res.data.error)
          setMessage('')
        }
        else{
          setMessage(res.data.message)
        setError('')
        setEmail('')
        }
        
      })
      .catch(err=>{
        setError("Invalid email try again")
          setMessage('')
      })
      
    }
    return (
        <Layout>
            <Col md="9" className="backsignin">
            <div className="animate__animated animate__pulse backsignin1">
            <h1 className="signinhead">Forgot Password</h1>
            {error?<div className="alert alert-danger">{error}</div>:''}
            {message?<div className="alert alert-info">{message}</div> : ''}
            <MyInput label={"E-Mail"} type={"email"} value={email} setValue={setEmail}/>
            <br/>
            <button className="homebtn signinbtn"  onClick={e=>handleSubmit(e)}>Forgot Password</button>

            <div style={{paddingBottom:"50px",marginTop:"10px"}}>
            <span style={{float:"left"}}>
              New User??
            <Link to="/signup"><span className="signinspan">Sign Up</span></Link>
            </span>
            <span style={{float:"right"}}>
                
            <Link to="/signin"><span className="signinspan">Already a User??</span></Link>
            </span>
            </div>

            </div>
            
        
            </Col>
        </Layout>    
        
       
    )
  }
  
  export default ForgotPassword;