import React, { useState,useEffect } from 'react';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import MobileStepper from '@material-ui/core/MobileStepper';
import Button from '@material-ui/core/Button';
import KeyboardArrowLeft from '@material-ui/icons/KeyboardArrowLeft';
import KeyboardArrowRight from '@material-ui/icons/KeyboardArrowRight';
import { getLocalStorage, isAuth ,removeLocalStorage,setLocalStorage} from '../../auth/window';
import { Modal, ModalBody } from 'reactstrap';
import {useParams,useHistory} from 'react-router-dom'
import axios from 'axios';
import {getCookie} from '../../auth/window';
import { CountdownCircleTimer } from 'react-countdown-circle-timer'

import TimeLeft from './TimeLeft'

export default function QuestionStepper({quiz,questions}) {

  const theme = useTheme();
  const [activeStep, setActiveStep] = React.useState(0);
  const maxSteps = questions.length;
  const [ans,setAns]=useState()
  const [modal, setModal] = useState(false);
  const[selected,setSelected]=useState([])
  const toggle = () => setModal(!modal);
 
  const {id,resultId}= useParams()

  useEffect(async()=>{   
    let temp = [];
    if(questions){
    for(let i=0;i<questions.length;i++){
      temp.push(questions[i].userAnswer)
    }
  
    if(temp && temp.length>0){
    setSelected(temp);
    setAns(temp[0])
    }
  }

  },[questions])


  const viewAnswer = (ans) => {
    if(ans == '1'){
      return questions[activeStep].option1;
    }
    if(ans == '2'){
      return questions[activeStep].option2;
    }
    if(ans == '3'){
      return questions[activeStep].option3;
    }
    if(ans == '4'){
      return questions[activeStep].option4;
    }
  }


  const handleNext = async() => {
      let temp=[...selected];
      temp[activeStep]=ans;
      setSelected(temp);
      setLocalStorage(resultId,temp);
      setAns(temp[activeStep+1])
    setActiveStep((prevActiveStep) => prevActiveStep + 1);
  };


  const handleBack = () => {
    let temp=[...selected]
    setAns(temp[activeStep-1]);
    setActiveStep((prevActiveStep) => prevActiveStep - 1);
  };



  return (
      <div className="container-fluid" style={{textAlign: 'left'}}>
      <div className="row" >
    <div className="col-md-10 mt-4 p-3 Quiz" >
       
       <strong style={{}}>Question:- </strong>  &nbsp; {questions.length>0?questions[activeStep].question:""} 
       <hr/>
       <div className="row" >
       <div className={ ans=='1' ? `col-md-5 Option Active` : `col-md-5 Option`}>
      {questions.length>0?questions[activeStep].option1:""} 
      </div>
      <div className={ ans=='2' ? `col-md-5 Option Active` : `col-md-5 Option`} >
      {questions.length>0?questions[activeStep].option2:""}
      </div>
      <div className={ ans=='3' ? `col-md-5 Option Active` : `col-md-5 Option`} >
      {questions.length>0?questions[activeStep].option3:""}
      </div>
      <div className={ ans=='4' ? `col-md-5 Option Active` : `col-md-5 Option`} >
      {questions.length>0?questions[activeStep].option4:""}
      </div> 
      
      <div className= "col-md-12">
        <hr/>
        <p className= "Option"style={{margin:"auto",width:"600px",maxWidth:"100%", backgroundColor:"green"}}>
             Answer:- {questions.length>0?viewAnswer(questions[activeStep].answer):""}
         </p>

      </div> 
     </div>
     </div>
   

     <div className="col-md-10 mt-4 Quiz mb-4" >
      <MobileStepper
        style={{ borderRadius:"30px",backgroundColor:"white"}}
        steps={maxSteps}
        position="static"
        variant="text"
        activeStep={activeStep}
        nextButton={
            <Button 
            className="next"
            style={{backgroundColor:activeStep===maxSteps- 1?"white":"#2d3e4f"}}
             onClick={handleNext}
             disabled={activeStep===maxSteps- 1}>
              Next
              {theme.direction === 'rtl' ? <KeyboardArrowLeft /> : <KeyboardArrowRight />}
            </Button>
          }
          backButton={
            <Button 
            className="next"
             onClick={handleBack} style={{backgroundColor:activeStep===0?"white":"#2d3e4f"}}
             disabled={activeStep===0}>
              {theme.direction === 'rtl' ? <KeyboardArrowRight /> : <KeyboardArrowLeft />}
              Back
            </Button>
        }
      />
    </div>
    </div>
    </div>
    
  );
}
