import React ,{useEffect, useState} from 'react';
import { Page, Text, View, Document, StyleSheet,Image } from '@react-pdf/renderer';
import {
    Table,
    TableHeader,
    TableCell,
    TableBody,
    DataTableCell,
  } from "@david.kucsai/react-pdf-table";


const styles = StyleSheet.create({
    
  body: {
    paddingTop: 35,
    paddingBottom: 65,
    paddingHorizontal: 35,
    padding:"10px",
    backgroundColor:"#cf404d",
    color:"white"

  },
  title: {
    fontSize: 24,
    textAlign: "center",
  },
  author: {
    fontSize: 12,
    textAlign: "center",
    marginBottom: 40,
  },
  subtitle: {
    fontSize: 18,
    margin: 12,
    textAlign: "center",
  },
  text: {
    margin: 12,
    fontSize: 14,
    textAlign: "justify",
  },
  image: {
    marginVertical: 15,
    marginHorizontal: 100,
  },
  header: {
    fontSize: 12,
    marginBottom: 10,
    textAlign: "center",
    color: "black",
  },
  footer: {
    padding: "100px",
    fontSize: 12,
    marginBottom: 20,
    textAlign: "center",
    color: "black",
  },
  pageNumber: {
    position: "absolute",
    fontSize: 12,
    bottom: 30,
    left: 0,
    right: 0,
    textAlign: "center",
    color: "grey",
  },
});

const ReactPdf =({result}) =>{
    const [name,setName]=useState('')

    useEffect(() =>{
        if(result && result.length>0){
            setName(result[0].quiz.name)
        }
    },[])
    return(
        
        <Document>
        <Page size="A4" style={styles.body}>
        <Text style={styles.header} fixed>
        ~ {new Date().toLocaleString()} ~
      </Text>
      <Text style={styles.title}>Quizee</Text>
      <Text style={styles.author}>Result</Text>
      <Text style={styles.subtitle}>{name}</Text>
      <Table >
        <TableHeader >
          <TableCell style={{textAlign: 'center'}}> Student Name </TableCell>
          <TableCell style={{textAlign: 'center'}} > Marks </TableCell>
        </TableHeader>
      </Table>
      <Table data={result}  >
        <TableBody>
          <DataTableCell style={{textAlign: 'center'}} getContent={(x) => x.student.name} />
          <DataTableCell style={{textAlign: 'center'}} getContent={(x) => x.marks} />
        </TableBody>
      </Table>
         
          <Text style={styles.footer}> ~ Thank you for using Quizee ~ </Text>          
        </Page>
      </Document>
      
    )
}

export default ReactPdf;