import React, { useState,useEffect }from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Timeline from '@material-ui/lab/Timeline';
import TimelineItem from '@material-ui/lab/TimelineItem';
import TimelineSeparator from '@material-ui/lab/TimelineSeparator';
import TimelineConnector from '@material-ui/lab/TimelineConnector';
import TimelineContent from '@material-ui/lab/TimelineContent';
import TimelineOppositeContent from '@material-ui/lab/TimelineOppositeContent';
import TimelineDot from '@material-ui/lab/TimelineDot';
import FastfoodIcon from '@material-ui/icons/Fastfood';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import ListAltIcon from '@material-ui/icons/ListAlt';
import UserModal from './UserModal'

const useStyles = makeStyles((theme) => ({
  paper: {
    padding: '6px 16px',
  },
  secondaryTail: {
    backgroundColor: theme.palette.secondary.main,
  },
}));

export default function CustomizedTimeline( { quizes,upcomingQuizes,timelineQuizes } ) {
  const classes = useStyles();
  const today= new Date()


  return (
   
    <Timeline align="alternate">
       {timelineQuizes.length>0? timelineQuizes.map((q,key)=>{
         if(key!=timelineQuizes.length-1){
         return(
          <TimelineItem key={key} >
          <TimelineOppositeContent >
          <Typography variant="h6" component="h3" style={{color:"#2d3e4f",fontSize:"18px",fontWeight:"550"}}>
              <strong>{q.startTime? q.startTime.split('T')[0]:""} at  {q.startTime?q.startTime.split('T')[1]:""} </strong>
              </Typography>
           
          </TimelineOppositeContent>
          <TimelineSeparator>
            <TimelineDot style={{backgroundColor:"#2d3e4f"}} >
              <ListAltIcon style={{color:"#fff"}} />
            </TimelineDot>
            <TimelineConnector />
          </TimelineSeparator>
          <TimelineContent style={{width:"100%"}}>
            <Paper elevation={3} className={classes.paper}>
              <Typography variant="h6" component="h1">
              <UserModal q={q} name={q.name} />

              </Typography>
            </Paper>
          </TimelineContent>
        </TimelineItem>
         )
         }
        else{
          return(
            <TimelineItem >
               <TimelineOppositeContent>
               <Typography variant="h6" component="h3" style={{color:"#2d3e4f",fontSize:"18px",fontWeight:"550"}}>
              {q.startTime? q.startTime.split('T')[0]:""} at  {q.startTime?q.startTime.split('T')[1]:""}
              </Typography>
          </TimelineOppositeContent>
            <TimelineSeparator>
            <TimelineDot style={{backgroundColor:"#2d3e4f"}} >
              <ListAltIcon style={{color:"#fff"}} />
            </TimelineDot>
            </TimelineSeparator>
            <TimelineContent>
              <Paper elevation={3} className={classes.paper}>
                <Typography variant="h6" component="h1">
                <UserModal q={q} name={q.name} />

                </Typography>
              </Paper>
            </TimelineContent>
          </TimelineItem>
          )
        }
        }
         
         ) : <div style={{textAlign: 'center', color:"black"}}>There are no quiz in upcoming three days... </div> }
      
    </Timeline>
        
  );
}
