import React from 'react';
import {useState} from 'react';
import UserTab from '../../components/User/UserTab'
import Layout from '../../components/Layout'
import { Col } from 'reactstrap';
import MyInput from '../../components/MyInput2'
import axios from 'axios';
import { Button, Modal, ModalHeader, ModalBody, ModalFooter } from 'reactstrap';
import { toast } from "react-toastify";
import LightTooltip from '../../components/LightTooltip'


import { getCookie } from '../../auth/window';

import AddIcon from '@material-ui/icons/Add';
import { useEffect } from 'react';



const UserDashboard=()=>{
    const [modal,setModal] = useState(false);
    const [email,setEmail] = useState('')
    const [quizCode, setQuizCode] = useState('')
    const [error,setError] = useState('')
    const [quizes,setQuizes] = useState([])
    const [upcomingQuizes,setUpcomingQuizes] = useState([])
    const [oldQuizes,setOldQuizes] = useState([])
    const [timelineQuizes,setTimelineQuizes] = useState([])
    const today= new Date()
    const toggle = () => setModal(!modal);
    
    const token=getCookie('token')
    const user = JSON.parse(localStorage.getItem('user'))
    useEffect(()=>{
        
        setEmail(user.email)
        viewQuiz()
    },[])

    const addQuiz = (e) => {
        e.preventDefault();
        if(!quizCode){
            setError('Please enter the quiz code received on email')
            return
        }

        axios.put(`${process.env.REACT_APP_API}/user/${email}`,{quizCode}, {
            headers: {
                'Content-Type': 'application/json',
                Authorization: `Bearer ${token}`
            }
        }).then((res) => {
            if(!res){
                return
            }
            setQuizCode('')
            setError('')
            toggle()
            viewQuiz()
            toast.success(`${res.data.message}`, {
                position: "top-center",
                });
        })
        .catch(err => {
            setError('Enter a valid quiz code')
        })
    }

    const viewQuiz = () => {
        axios.get(`${process.env.REACT_APP_API}/user-quizes/${user.email}`,{
            headers:{
                "Content-Type": "application/json",
                Authorization: `Bearer ${token}`
            }
        }).then(res => {
            setQuizes(res.data)
            let temp=[]
            let temp1=[]
            let temp2=[]

            for(let i=0;i<res.data.length;i++){
                let upcoming=new Date(res.data[i].startTime);
                let diffTime=( today-upcoming)
                let diffMin=Math.ceil(diffTime / (1000 * 60 ));
                if(upcoming>today ||(diffMin< res.data[i].duration && diffMin>=0)){
                    temp.push(res.data[i])
                }
                else{
                    temp1.push(res.data[i])
                }
            }
            for(let i=0;i<temp.length;i++){
                let upcoming=new Date(temp[i].startTime);
                let diffTime=( upcoming-today)
                let diffDay=Math.ceil(diffTime / (1000 * 60 *60*24));
                if( diffDay <=3){
                    temp2.push(temp[i])
                }
        
            }
            setTimelineQuizes(temp2)
            setOldQuizes(temp1.reverse())
            setUpcomingQuizes(temp)
        })
        .catch(err => {console.log(err)})
    }

    return (
        <Layout>
            <Col md="9" className="back2">
            <div className="aboutText">
                <h1 className="abouth1">Dashboard</h1>                
            </div>
            <LightTooltip title="Add Quiz">
            <div 
            className="AddButton back3"
            onClick={toggle} >
                <AddIcon 
                    className="addIcon"
                 />
            </div>
            </LightTooltip>
            <UserTab quizes={quizes} oldQuizes={oldQuizes} upcomingQuizes={upcomingQuizes} timelineQuizes={timelineQuizes}/>
            </Col>


            {/* modal to add question */}
            <Modal isOpen={modal} toggle={toggle} className="my-modal mb-4 "  size="lg"
                centered={true}
                style={{textAlign: 'center', maxWidth:"600px" }}
                >
                <ModalBody >
                    <div className="mt-2 mb-4" style={{fontSize:"30px"}}> Enter Quiz Code </div>
                    {error?<div className="alert alert-danger">{error}</div>:""}
                    
                    <MyInput label={"Quiz Code"} type={"text"}
                     value={quizCode} setValue={setQuizCode} clas={'Active'}/>
                    
                </ModalBody>
                <div style={{textAlign: 'center',paddingBottom:"20px"}}> 
                  <Button color="homebtn profilebtn"
                  style={{fontSize:"20px", padding:"5px 20px", fontFamily:"cursive"}}
                  onClick={e => addQuiz(e)}
                  >
                      Save</Button>{'   '}
                  <Button
                  style={{fontSize:"20px", padding:"5px 20px", fontFamily:"cursive"}} 
                  color="homebtn profilebtn2" onClick={toggle}>Cancel</Button>
                  </div>
              </Modal>
              {/* modal to add question end */}
        </Layout>
    )
}

export default UserDashboard;