import React from 'react';
import {Link, useHistory} from 'react-router-dom'
import { Row, Col } from 'reactstrap';
import styles from '../css/Layout.css'
import axios from 'axios'
import {isAuth, removeCookie,removeLocalStorage} from '../auth/window'
import { toast } from "react-toastify";
import Header from './Header';
import HomeIcon from '@material-ui/icons/Home';
import InfoIcon from '@material-ui/icons/Info';
import ContactSupportIcon from '@material-ui/icons/ContactSupport';
import PersonAddIcon from '@material-ui/icons/PersonAdd';
import GroupIcon from '@material-ui/icons/Group';
import AccountCircleIcon from '@material-ui/icons/AccountCircle';
import ExitToAppIcon from '@material-ui/icons/ExitToApp';
import TimelineIcon from '@material-ui/icons/Timeline';

function Layout({children}) {
    const history =useHistory()
    
    const signout =() =>{
        removeCookie('token');
        localStorage.clear();
        axios.get(`${process.env.REACT_APP_API}/signout`,{},{
          headers: {}  
        }).then((res)=>{
            toast.success('Logout Successfully', {
                position: "top-center",
                });
        history.push("/signin")
        }).catch((err)=>console.log(err))
    }
    return (   
        <div>
            <Header/>
        <div className="container-fluid">
            
            <Row>
            <Col md="3" className="header1" style={{height:"100%",position:"fixed",left:"0",backgroundColor:"#2D3E4F"}}>
                <div style={{textAlign:"center"}}>
                <Link to="/" ><h1 className="brandText" style={{marginTop:"50px"}}>
                <img className="" 
                src="https://res.cloudinary.com/quizee/image/upload/v1625986661/Logo_red_t_ln3kfa.png"
                style={{maxWidth:"100%",maxHeight:"100px"}}
                />
                    </h1></Link>
                {isAuth()?<h5 style={{color:"rgba(238, 240, 219, 0.705)",marginTop:"-15px",marginBottom:"40px"}} className="">Hi {isAuth().name}</h5>:""}
                <div style={{margin:"auto"}}>
                <ul style={{listStyleType:"none",textAlign:"justify",margin:"0px",padding:"0px",margin:"auto",maxWidth:"fit-content"}}>
                
                {!isAuth() ?(
                     <>
                     <li><Link to="/" ><h5  className="navlink"> 
                     <HomeIcon
                     className="navIcon"
                     /> Home</h5></Link></li>
                     <li><Link to="/about"><h5 className="navlink">
                     <InfoIcon
                     className="navIcon"
                     /> About</h5></Link></li>
                     <li><Link to="/contact"><h5  className="navlink">
                     <ContactSupportIcon
                     className="navIcon"
                     /> Contact us</h5></Link></li>
                     <li><Link to="/facultyrequest"><h5  className="navlink">
                     <PersonAddIcon
                     className="navIcon"
                     /> Faculty Request</h5></Link></li>
                     </>
                )
                :
                isAuth().role==='0'|| isAuth().role==="19"?
                (
                    <>
                     <li><Link to="/admindashboard"><h5 className="navlink">
                     <HomeIcon
                     className="navIcon"
                     /> Home</h5></Link></li>
                     <li><Link to='/profile' ><h5  className="navlink">
                     <AccountCircleIcon
                     className="navIcon"
                     /> Profile</h5></Link></li>
                     </>
                )
                :
                isAuth().role==='1' ?
                (
                    <>
                     <li><Link to="/facultydashboard"><h5 className="navlink">
                     <HomeIcon
                    className="navIcon"
                     /> Home</h5></Link></li>
                     <li><Link to='/profile' ><h5  className="navlink">
                     <AccountCircleIcon
                     className="navIcon"
                     /> Profile</h5></Link></li>
                     <li><Link to="/contact"><h5  className="navlink">
                     <ContactSupportIcon
                     className="navIcon"
                     /> Contact us</h5></Link></li>
                     </>
                ):
                (
                    <>
                    <li><Link to="/dashboard"><h5 className="navlink">
                    <HomeIcon
                     className="navIcon"
                     /> Home</h5></Link></li>
                    <li><Link to='/profile' ><h5  className="navlink">
                    <AccountCircleIcon
                     className="navIcon"
                     /> Profile</h5></Link></li>
                    <li><Link to="/contact"><h5  className="navlink">
                    <ContactSupportIcon
                     className="navIcon"
                     /> Contact us</h5></Link></li>
                    </>
                )
                }
                <li><Link to="/team"><h5  className="navlink">
                    
                <GroupIcon
                     className="navIcon"
                     /> our team</h5></Link></li>
                {
                    isAuth() ?
                    <li><Link to='#' onClick={signout}><h5  className="navlink">
                      <ExitToAppIcon
                     className="navIcon"
                     /> Signout</h5></Link></li>
                    :""
                }
                </ul>
                </div>
                </div>
            </Col>
            
        {children} 
        
            </Row>
            
        </div>
        </div>
       
    )
  }
  
  export default Layout;