import React from 'react';
import Layout from '../../components/Layout'
import { Col } from 'reactstrap';
import Axios from 'axios';
import MyInput from '../../components/MyInput'
import { useState } from 'react';

const FacultyRequest=()=>{

    const [email, setEmail] = useState('');
    const [name, setName] = useState('');
    const [number, setNumber] = useState('');
    const [aadhar, setAadhar] = useState('');
    const [error, setError] = useState('');
    const [message, setMessage] = useState('');
    
    const handleSubmit=(e)=>{
        e.preventDefault();
        if(!name || !email || !number || !aadhar){
            setMessage("")
            setError("Please enter all the fields")
            return 
        }
        if(!/^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/.test(email)){
            setError('Please Enter valid Email id')
            setMessage('')
            return
        }

        let data={
            email,
            name,
            number,
            aadhar
        }
        Axios.post(`${process.env.REACT_APP_FACULTY_REQUEST_API}`,data)
        .then(res=>{
            setError("")
            setMessage("Your Query is submitted admin will contact you soon...")
            setEmail('');
            setName('');
            setNumber('');
            setAadhar('');
        })
        .catch(err=>{
            setError(err);
            setMessage("")
            console.log(err)
        })
       
    }

    return (
        <Layout>
        <Col md="9" className="back2">
            <div className="aboutText" >
                <h1 className="abouth1">Faculty Request</h1>
                <br/>
                <h6 className="abouth6">You can put your request forward</h6>
            </div>
                <div className="backsignin1 " style={{marginTop:"1%"}}>
                {error?<div className="alert alert-danger">{error}</div>:""}
            {message?<div className="alert alert-info">{message}</div> : ''}
                <MyInput label={"Name"} type={"text"} value={name} setValue={setName} />
                <MyInput label={"E-Mail"} type={"email"} value={email} setValue={setEmail}/>
                <MyInput label={"Contact No."} type={"text"} value={number} setValue={setNumber}/>
                <MyInput label={"Aadhar Number"} type={"text"} value={aadhar} setValue={setAadhar}/>
                
            <br/>
            <button className="homebtn signinbtn" onClick={e=>handleSubmit(e)}>Submit</button>
            </div>
            
            
            </Col>
        
        </Layout>
            
    )
}
export default FacultyRequest;