import {Link,useHistory} from 'react-router-dom'
import React, { useState, useEffect } from 'react';
import GoogleLogin from 'react-google-login';
import axios from 'axios';
import {  authenticate, isAuth } from '../auth/window';
import { toast } from "react-toastify";


const LoginGoogle = () => {
    const history=useHistory();

    const responseGoogle = response => {
        const tokenId = response.tokenId;
        const user = { tokenId };
        axios.post(`${process.env.REACT_APP_API}/google-signin`,user,
        {
            headers: {
                "Content-Type": "application/json"
            }
        })
        .then(res=>{
            if (res.error) {
                console.log(res.error);
            } else {
                const data={
                    token:res.data.signinToken,
                    user:{
                      email:res.data.email,
                      role:res.data.role,
                      name:res.data.name,
                      dob:res.data.dob,
                      number:res.data.mobile_number,
                      pic:res.data.pic,
                      id:res.data._id
                    }
                  }
                authenticate(data);
                toast.success('Sign in Successfully', {
                    position: "top-center",
                    });
                if(isAuth() && (isAuth().role === '0' || isAuth().role === '19'))
                    history.push("/admindashboard")
                else if(isAuth() && isAuth().role === '1')
                    history.push("/facultydashboard")
                else
                    history.push("/dashboard")
            
            }
        })
        .catch(err => console.log(err))


        // loginWithGoogle(user).then(data => {
        //     if (data.error) {
        //         console.log(data.error);
        //     } else {
        //         authenticate(data, () => {
        //             if(isAuth() && isAuth().role === '0')
        //                 history.push("/admindashboard")
        //             else if(isAuth() && isAuth().role === '1')
        //                 history.push("/facultydashboard")
        //             else
        //                 history.push("/dashboard")
        //         });
        //     }
        // });
    };

    return (
        <div className="pt-4" >
            <GoogleLogin
                clientId={`${process.env.REACT_APP_GOOGLE_CLIENT_ID}`}
                buttonText="Login with Google"
                onSuccess={responseGoogle}
                onFailure={responseGoogle}
                theme="dark"
            />
        </div>
    );
};

export default LoginGoogle;