import React, { useState,useEffect } from 'react';
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import axios from 'axios';
import MyInput from '../MyInput2'
import FacultyResult from './FacultyResult'

import { isAuth,getCookie } from '../../auth/window';
import { Button, Modal, ModalHeader, ModalBody, ModalFooter } from 'reactstrap';
import EditIcon from '@material-ui/icons/Edit';
import DeleteSweepIcon from '@material-ui/icons/DeleteSweep';
import Accordian from './Accordian';

function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box p={3}>
          <Typography>{children}</Typography>
        </Box>
      )}
    </div>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.any.isRequired,
  value: PropTypes.any.isRequired,
};

function a11yProps(index) {
  return {
    id: `simple-tab-${index}`,
    'aria-controls': `simple-tabpanel-${index}`,
  };
}

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
    backgroundColor: theme.palette.background.paper,
  },
}));

export default function FacultyTab({que,viewQuestion,allStudents,id,ViewQuiz,quiz, result}  ) {
  const classes = useStyles();
  const token=getCookie('token')
  const [value, setValue]= React.useState(0);
  const [modal, setModal] = useState(false);
  const [modal1, setModal1] = useState(false);
  const [modal2, setModal2] = useState(false);
  const [quesId, setQuesId] = useState('');
  const [studentid, setStudentid] = useState('');
  const [message,setMessage]=useState('')
  const [error,setError]=useState('')
  const [question, setQuestion] = useState("");
  const [option1, setOption1] = useState("");
  const [option2, setOption2] = useState("");
  const [option3, setOption3] = useState("");
  const [option4, setOption4] = useState("");
  const [answer, setAnswer] = useState("");
  // const [result, setResult] = useState([])
  let chartdata=[["result", "marks"]]
  let data=[]
  const toggle = () => setModal(!modal);
  const toggle2 = () => setModal2(!modal2);
  const toggle1 = () => {
    setModal1(!modal1);
    setQuesId('');
    setQuestion('')
    setOption1('')
    setOption2('')
    setOption3('')
    setOption4('')
    setAnswer('')
  }
  
  let now=new Date();
  let startTime=new Date(quiz.startTime);  

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  const deleteQuestion = (e,id) => {
    setQuesId(id);
    toggle();
  }
  const deleteStudent = (e,id) => {
    setStudentid(id);
    toggle2();
  }
const handleDelete = (e) =>{
        e.preventDefault();

        axios.delete(`${process.env.REACT_APP_API}/delete-question/${quesId}`,{
          headers:{
              "Content-Type":'application/json',
              "Authorization": `Bearer ${token}`
              }   
      })
      .then(res=>{
          if(res.data){
          toggle()
          setError('')
          setMessage(``)
          setQuesId('')
          viewQuestion()
          }
          else if(res.error){
              setError(res.error,"error")
              setMessage('')
          }
          
      }).catch(err=>{
          setError(`err`, err)
          setMessage('')
          console.log(err, 'hello')
      })
        
      }

      const handleEditQuestion = (e, q) => {
        toggle1()
        setQuesId(q._id);
        setQuestion(q.question);
        setOption1(q.option1);
        setOption2(q.option2);
        setOption3(q.option3);
        setOption4(q.option4);
        setAnswer(q.answer);
      }

      const updateQuestion=(e) => {
        e.preventDefault();
        if(!question || !option1 || !option2 || !option3 || !option4 || !answer){
          setError("Please enter all fields")
          return;
      }
      if(answer <1 && answer >4){
          setError("Please enter answer in given range")
          return 
      }
      axios.put(`${process.env.REACT_APP_API}/update-question`,{
          _id:quesId, question, option1, option2,option3,option4, answer
      },{
          headers:{
              Authorization:`Bearer ${token}`,
              "Content-Type":"application/json"
          }
      }).then(res => {
          viewQuestion()      
          toggle1()
      })
      .catch(err => console.log(err))
  }

  const removeStudent = (e) =>{
    e.preventDefault();

        axios.put(`${process.env.REACT_APP_API}/remove-student/${id}/${studentid}`,{},{
          headers:{
              "Content-Type":'application/json',
              "Authorization": `Bearer ${token}`
              }   
      })
      .then(res=>{
        toggle2()
        ViewQuiz()
        
      })
      .catch(err=>console.log(err))
  }


  return (
    <div className={classes.root}>  
      <AppBar position="static">
        <Tabs value={value} onChange={handleChange} aria-label="simple tabs example" style={{backgroundColor:"#2D3E4F"}}>
          <Tab className='vTab vTab2' label="View Question " {...a11yProps(0)} />
          <Tab className='vTab vTab2' label="View All Students" {...a11yProps(1)} />
          {now-startTime>0?<Tab className='vTab vTab2' label="View Result" {...a11yProps(2)} />:" "}
        </Tabs>
      </AppBar>
      <TabPanel  value={value} index={0}>
      
        {que.length>0?que.map((q,key)=>{
            return(
              <Accordian q={q} i={key} handleEditQuestion={handleEditQuestion} deleteQuestion={deleteQuestion}/>
            )
        })
        :""
        
        }
      
      </TabPanel>
      <TabPanel value={value} index={1}>
      <div className="container-fluid"  style={{backgroundColor:"#cf404d",padding:" 0px ", marginBottom:"10px",marginTop:"10px"}}>
           <div className="row m-0">
                <div className="col-8 offset-1" style={{textAlign: '',marginTop:"8px"}}>
                    <h5 style={{fontWeight:"bold", color:"white"}}>Email</h5>

                        </div>
                        <div className="col-2" style={{textAlign: 'center',marginTop:"8px"}}>
                        <h5 style={{fontWeight:"bold", color:"white"}}>Remove</h5>
                        </div>                                             
                    </div>
                </div>
          {allStudents.length>0?allStudents.map((p,key)=>{
            return(
             <div className="container" key={key} style={{backgroundColor:"rgba(241, 241, 235, 0.705)",padding:"15px 0px ", marginBottom:"10px"}}>
             <div className="row m-0">
              <div className="col-8 offset-1" style={{textAlign: 'left'}}>
                        <h6 style={{fontWeight:"bold", color:"#2D3E4F"}}>{p}</h6>

                  </div>
                     
                  <div className="col-2" style={{textAlign: 'center',marginTop:""}}>
                  <h6 >
                  <DeleteSweepIcon
                  onClick={e=>deleteStudent(e,p)}
                  style={{fontSize:"30px", cursor:"pointer", color:"#cf404d"}}
                   />
                  </h6>
                  </div>
              </div>
              </div>
            )
              
          })
          :""
          }
      </TabPanel>
          
      <TabPanel  value={value} index={2}>
          <FacultyResult result={result} />
      </TabPanel>



        <Modal isOpen={modal} toggle={toggle} className="my-modal mb-4 "  size="lg"
                centered={true}
                style={{textAlign: 'center', maxWidth:"600px" }}
                >
                <ModalBody >
                    <div className="mt-2 mb-4" style={{fontSize:"30px"}}> Are you sure you want to delete ? </div>
                    
                </ModalBody>
                <div style={{textAlign: 'center',paddingBottom:"20px"}}> 
                  <Button color="homebtn profilebtn"
                  style={{fontSize:"20px", padding:"5px 20px", fontFamily:"cursive"}}
                  onClick={e=>handleDelete(e)}>
                      Yes</Button>{'   '}
                  <Button
                  style={{fontSize:"20px", padding:"5px 20px", fontFamily:"cursive"}} 
                  color="homebtn profilebtn2" onClick={toggle}>No</Button>
                  </div>
              </Modal>
    
        <Modal isOpen={modal1} toggle={toggle1} className="my-modal mb-4 "  size="lg"
                centered={true}
                style={{textAlign: 'center' }} 
                >
                <ModalBody >
                    <div className="mt-2 mb-4" style={{fontSize:"30px"}}> Add New Quiz </div>
                    {error?<div className="alert alert-danger">{error}</div>:""}
                    <textarea 
                    placeholder="Question" 
                    type="text"
                    value={question} 
                    rows="3"
                    style={{width:"100%"}}
                    onChange={e=>setQuestion(e.target.value)} />
                    <MyInput label={"Option 1"} type={"text"}
                     value={option1} setValue={setOption1} clas={'Active'}/>
                    <MyInput label={"Option 2"} type={"text"}
                     value={option2} setValue={setOption2} clas={'Active'}/>
                    <MyInput label={"Option 3"} type={"text"}
                     value={option3} setValue={setOption3} clas={'Active'}/>
                    <MyInput label={"Option 4"} type={"text"}
                     value={option4} setValue={setOption4} clas={'Active'}/>
                    <MyInput label={"Answer (value from 1,2,3,4)"} type={"number"}
                     value={answer} setValue={setAnswer} clas={'Active'}/>



                </ModalBody>
                <div style={{textAlign: 'center',paddingBottom:"20px"}}> 
                  <Button color="homebtn profilebtn"
                  style={{fontSize:"20px", padding:"5px 20px", fontFamily:"cursive"}}
                  onClick={e=>updateQuestion(e)}
                  >
                      Save</Button>{'   '}
                  <Button
                  style={{fontSize:"20px", padding:"5px 20px", fontFamily:"cursive"}} 
                  color="homebtn profilebtn2" onClick={toggle1}>Cancel</Button>
                  </div>
              </Modal>

          <Modal isOpen={modal2} toggle={toggle2} className="my-modal mb-4 "  size="lg"
        centered={true}
        style={{textAlign: 'center', maxWidth:"600px" }}
        >
        <ModalBody >
            <div className="mt-2 mb-4" style={{fontSize:"30px"}}> Are you sure you want to delete ? </div>
            
        </ModalBody>
        <div style={{textAlign: 'center',paddingBottom:"20px"}}> 
          <Button color="homebtn profilebtn"
          style={{fontSize:"20px", padding:"5px 20px", fontFamily:"cursive"}}
          onClick={e=>removeStudent(e)}>
              Yes</Button>{'   '}
          <Button
          style={{fontSize:"20px", padding:"5px 20px", fontFamily:"cursive"}} 
          color="homebtn profilebtn2" onClick={toggle2}>No</Button>
          </div>
      </Modal>

    </div>
  );
}
