import React, { useState } from 'react';
import {
  Collapse,
  Navbar,
  NavbarToggler,
  NavbarBrand,
} from 'reactstrap';
import PersistentDrawerLeft from './Drawer1';

const Header = (props) => {
  const [isOpen, setIsOpen] = useState(false);

  const toggle = () => setIsOpen(!isOpen);

  return (
    <div className="header2">
      <PersistentDrawerLeft toggle={toggle} isOpen={isOpen} setIsOpen={setIsOpen} />
    </div>
  );
}

export default Header;
