import React, { useState,useEffect } from 'react';
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import MyCard from '../MyCard';
import VerticalTab from './VerticalTab1';
import VerticalTab2 from './VerticalTab2';
import VerticalTab3 from './VerticalTab3';
import axios from 'axios';
import { isAuth,getCookie, authenticate } from '../../auth/window';
import CancelIcon from '@material-ui/icons/Cancel';
import { Button, Modal, ModalHeader, ModalBody, ModalFooter } from 'reactstrap';

function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      {value === index && (
        <Box p={3}>
          <Typography>{children}</Typography>
        </Box>
      )}
    </div>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.any.isRequired,
  value: PropTypes.any.isRequired,
};

function a11yProps(index) {
  return {
    id: `simple-tab-${index}`,
    'aria-controls': `simple-tabpanel-${index}`,
  };
}

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
    backgroundColor: theme.palette.background.paper,
  },
}));

export default function AdminTab() {
  const classes = useStyles();
  const token=getCookie('token')
  const [viewAdmin, setViewAdmin] = React.useState();
  const [value, setValue] = React.useState(0);
  const [modal, setModal] = useState(false);
  const [emailid, setEmailid] = React.useState('');

  const toggle = () => setModal(!modal);

  const toggle1 = (mailid) => {
    setEmailid(mailid);
    toggle();
  }
  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

 
  const handleView=async()=>{
    
    await axios.get(`${process.env.REACT_APP_API}/all-student`,{
      headers:{
          "Content-Type": "application/json",
          Authorization: `Bearer ${token}`
      }
    })
    .then(res=>{
      setViewAdmin(res.data.users)
    })
    .catch(e=>console.log(e))
}


const handleDelete = (e) =>{
  e.preventDefault();
  const data={
    email:emailid
  }
  axios.delete(`${process.env.REACT_APP_API}/remove-user/${emailid}`,{
    headers:{
        "Content-Type":'application/json',
        "Authorization": `Bearer ${token}`
        }   
})
.then(res=>{
    if(res.data){
    toggle()
    setEmailid('')
    handleView()
    }
    else if(res.error){
        console.log(res)
        
    }
    
}).catch(err=>{
    console.log(err, 'hello')
})
  
}

  return (
    <div className={classes.root}>
      <AppBar position="static">
        <Tabs value={value} onChange={handleChange} aria-label="simple tabs example" style={{backgroundColor:"#2D3E4F"}}>
         
          <Tab className='vTab vTab2' label="Faculty" {...a11yProps(0)} />
          <Tab onClick={e=>handleView(e)} className='vTab vTab2' label="Student" {...a11yProps(1)} />
          <Tab className='vTab vTab2' label="Queries" {...a11yProps(2)} />
          {isAuth().role==='19'? <Tab className='vTab vTab2' label="Admin" {...a11yProps(3)} /> : ""}
        </Tabs>
      </AppBar>
      <TabPanel  value={value} index={3}>
        <VerticalTab />
      </TabPanel>
      <TabPanel value={value} index={0}>
      <VerticalTab2 />
      </TabPanel>
      <TabPanel value={value} index={1}>
      <div className="container-fluid"  style={{backgroundColor:"#cf404d",padding:" 0px ", marginBottom:"10px"}}>
      <div className="row">
                    <div className="col-2" style={{textAlign: 'center',marginTop:"8px"}}>
                           <h5 style={{fontWeight:"bold", color:"white"}}>Image</h5>
                        </div>
                        <div className="col-3" style={{textAlign: 'center',marginTop:"8px"}}>
                           <h5 style={{fontWeight:"bold", color:"white"}}>Name</h5>
                        </div>
                        <div className="col-5" style={{textAlign: 'center',marginTop:"8px"}}>
                        <h5 style={{fontWeight:"bold", color:"White"}}>Email</h5>
                        </div>
                        <div className="col-2" style={{textAlign: 'center',marginTop:"8px"}}>
                        <h5 style={{fontWeight:"bold", color:"White"}}>Remove</h5>
                        </div>
                    </div>
                </div>
        {viewAdmin?viewAdmin.map((p,key)=>{
            return(
              <div className="container-fluid" key={key} style={{backgroundColor:"rgba(241, 241, 235, 0.705)",padding:"15px 0px ", marginBottom:"10px"}}>
              <div className="row">
                  <div className="col-2" style={{textAlign: 'center'}}>
                      <img
                      style={{ width:"40px", height:"40px", borderRadius:"100%"}} 
                      src={p.pic}/>
                  </div>
                  <div className="col-3" style={{textAlign: 'center',marginTop:"8px"}}>
                     <h6 style={{fontWeight:"bold", color:"#2D3E4F"}}>{p.name}</h6>
                  </div>
                  <div className="col-5" style={{textAlign: 'center',marginTop:"8px"}}>
                  <h6 style={{fontWeight:"bold", color:"#2D3E4F"}}>{p.email}</h6>
                  </div>
                  <div className="col-2" style={{textAlign: 'center',marginTop:"8px"}}>
                  
                  <CancelIcon
                  onClick={e=>toggle1(p.email)}
                style={{
                  textAlign: 'center',
                  fontSize:"30px", 
                  color: '#cf404d',
                  margin:"auto",
                  cursor: 'pointer'}} 
                />
                  </div>
              </div>
                </div>
            )
        }):''}
      </TabPanel>
      <TabPanel value={value} index={2}>
        <VerticalTab3 />
      </TabPanel>
      
      <div>
      <Modal isOpen={modal} toggle={toggle} className="my-modal mb-4 "  size="lg"
                centered={true}
                style={{textAlign: 'center', maxWidth:"600px" }}
                >
                <ModalBody >
                    <div className="mt-2 mb-4" style={{fontSize:"30px"}}> Are you sure you want to delete {emailid}? </div>
                    
                </ModalBody>
                <div style={{textAlign: 'center',paddingBottom:"20px"}}> 
                  <Button color="homebtn profilebtn"
                  style={{fontSize:"20px", padding:"5px 20px", fontFamily:"cursive"}}
                  onClick={e=>handleDelete(e)}>
                      Yes</Button>{'   '}
                  <Button
                  style={{fontSize:"20px", padding:"5px 20px", fontFamily:"cursive"}} 
                  color="homebtn profilebtn2" onClick={toggle}>No</Button>
                  </div>
              </Modal>
    </div>
    </div>
  );
}
