import React, {  lazy, Suspense } from "react";
import { Switch, Route } from "react-router-dom";
import { ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";

import { AdminRoute, AuthRoute, FacultyRoute, PrivateRoute } from "./auth/routes";

import Signup from './pages/auth/Signup'
import VerifyAccount from "./pages/auth/VerifyAccount";
import Updateprofile from "./pages/auth/UpdateProfile";

import Signin from "./pages/auth/Signin";
import ForgotPassword from "./pages/auth/ForgotPassword";
import ResetPassword from "./pages/auth/ResetPassword";

import About from './pages/home/About'
import OurTeam from './pages/home/OurTeam'
import Contact from './pages/home/Contact'
import FacultyRequest from './pages/home/FacultyRequest'
import TermsAndCondition from './pages/home/TermsAndCondition'
import PrivacyPolicy from './pages/home/PrivacyPolicy'

import AdminDashboard from "./pages/admin/AdminDashboard";

import FacultyDashboard from "./pages/faculty/FacultyDashboard";
import Quiz from "./pages/faculty/Quiz";

import UserDashboard from "./pages/user/UserDashboard";
import StartQuiz from "./pages/user/StartQuiz";
import FinishQuiz from "./pages/user/FinishQuiz";

import Profile from "./pages/Profile";

const Home = lazy(()=>import('./pages/home/Home'));


const App = ()=> {
  return (
    <Suspense
      fallback={
        <div style={{position:'fixed',width:"100%",height:"100%",backgroundColor:"#2D3E4F"}}>
        <div className="loader" style={{marginTop:"18%"}}>
          Loading...
        </div>
        </div>
      }
    >
    <ToastContainer autoClose={2000} />
    <Switch>
    
    <Route exact path="/about" component={About} />
    <Route exact path="/team" component={OurTeam} />
    <Route exact path="/contact" component={Contact} />
    <Route exact path="/facultyrequest" component={FacultyRequest} />
    <Route exact path="/termsandcondition" component={TermsAndCondition} />
    <Route exact path="/privacypolicy" component={PrivacyPolicy} />
    <AuthRoute exact path="/signup" component={Signup} />
    <AuthRoute exact path="/signin" component={Signin} />
    <Route exact path="/forgotpassword" component={ForgotPassword} />
    <Route exact path="/resetpassword/:email/:token" component={ResetPassword} />
    <AuthRoute exact path="/updateprofile" component={Updateprofile} />
    <Route exact path="/verifyaccount/:email/:token" component={VerifyAccount} />
    <AdminRoute exact path="/admindashboard" component={AdminDashboard} />
    <FacultyRoute exact path="/facultydashboard" component={FacultyDashboard} />
    <FacultyRoute exact path="/facultydashboard/:id" component={Quiz} />
    <PrivateRoute exact path="/dashboard" component={UserDashboard} />
    <PrivateRoute exact path="/quiz/:id/:resultId" component={StartQuiz} />
    <PrivateRoute exact path="/finishquiz/:id" component={FinishQuiz} />
    <PrivateRoute exact path="/profile" component={Profile} />
    <Route path="/" component={Home} />
    </Switch>
    </Suspense>
  );
}

export default App;


