import React from 'react';
import Layout from '../../components/Layout'
import { Col } from 'reactstrap';
import {Link} from 'react-router-dom'
import MyInput from '../../components/MyInput'
import { useState } from 'react';
import axios from 'axios';
import LoginGoogle from '../../components/LoginGoogle'

function Signup() {
    const [email, setEmail] = useState('');
    const [message,setMessage]=useState('')
    const [error,setError]=useState('')

    const handleSubmit=(e)=>{
        e.preventDefault();
        if(!email){
            setError('Please enter Email id')
            setMessage('')
        }
        else if(!/^(([^<>()\[\]\\.,;:\s@"]+(\.[^<>()\[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/.test(email)){
            setError('Please Enter valid Email id')
            setMessage('')
        }
        else{
        axios.post(`${process.env.REACT_APP_API}/signup`,{email},{
            headers:{
                "Content-Type":'application/json',
                }   
        })
        .then(res=>{
            if(res.data){
            setError('')
            setMessage(`email has been sent successfully to ${email}`)
            setEmail('')
            }
            else if(res.error){

                setError(res.error,"error")
                setMessage('')
            }
            
        }).catch(err=>{
            setError(`email already exists try to login`)
            setMessage('')
            console.log(err)
        })

        }    }
    

    return (
        <Layout>
            <Col md="9" className="backsignin">
            <div className="animate__animated animate__pulse backsignin1">
            <h1 className="signinhead">Sign Up</h1>
            {error && error.length?<div className="alert alert-danger">{error}</div>:""}
            {message && message.length?<div className="alert alert-info">{message}</div> : ''}
            <LoginGoogle/>
            <MyInput label={"E-Mail"} type={"email"} value={email} setValue={setEmail}/>
            <br/>
            <button className="homebtn signinbtn" onClick={e=>handleSubmit(e)}>Sign Up</button>

            <div style={{paddingBottom:"50px",marginTop:"10px"}}>
            <span style={{float:"right"}}>
            <Link to="/signin"><span className="signinspan">Already a User?</span></Link>
            </span>
            </div>
            </div>
            </Col>
        </Layout>      
    )
  }
  
  export default Signup;