import React,{ useState,useEffect} from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import IconButton from '@material-ui/core/IconButton';
import Typography from '@material-ui/core/Typography';
import CloseIcon from '@material-ui/icons/Close';
import Slide from '@material-ui/core/Slide';
import axios from 'axios';
import {getCookie,isAuth,setLocalStorage,getLocalStorage} from '../../auth/window';
import {useHistory} from 'react-router-dom'
import ResultStepper from './ResultStepper'
import { Chart } from "react-google-charts";
import MyModal from './MyModal'



const useStyles = makeStyles((theme) => ({
  appBar: {
    position: 'relative',
  },
  title: {
    marginLeft: theme.spacing(2),
    flex: 1,
  },
}));

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />;
});

export default function FullScreenDialog({q,name="VIEW RESULT"}) {
  const classes = useStyles();
  const [open, setOpen] = React.useState(false);
  const [result, setResult] = React.useState([]);
  const [marks, setMarks] = React.useState(0);
  const [unans, setUnans] = React.useState(0);
  const [present, setPresent] = React.useState(false);
  const token=getCookie('token')
  let userId = isAuth().id;
  
  const [analysis, setAnalysis] = useState(false)

  const handleResult = ( id) => {
    axios.get(`${process.env.REACT_APP_API}/result-quiz-student/${id}/${userId}`,{
      headers:{
        'Content-Type': 'application/json',
        Authorization: `Bearer ${token}`
      }
    }).then(res => { 
        setResult(res.data.result.questions)
        setMarks(res.data.result.marks)
        let sum=0
        for(let i=0;i<res.data.result.questions.length;i++){
          if(res.data.result.questions[i].userAnswer =="0")
            sum+=1
        }
        setUnans(sum)
        console.log(res.data.result)
        setPresent(res.data.message)
    })
    .catch(err => console.log(err))
  }

  const handleClickOpen = () => {
    setOpen(true);
    handleResult(q._id)
  };

  const handleClose = () => {
    setOpen(false);
  };

  return (
    <div style={{zIndex:"100"}}>
      
       <Button variant="outlined" 
       style={{padding:"10px 30px",fontSize:"18px",borderRadius:"200px",border:"0px",textTransform:"none"}}
     onClick={handleClickOpen}>
        {name}

      </Button>
      <Dialog fullScreen open={open} onClose={handleClose} TransitionComponent={Transition}>
        <AppBar className={classes.appBar} style={{backgroundColor:"#cf404d"}}>
          <Toolbar>
            <IconButton edge="start" color="inherit" onClick={handleClose} aria-label="close">
              <CloseIcon />
            </IconButton>
            <Typography variant="h6" className={classes.title}>
              {q.name}
            </Typography>
            {present=="true"?
            !analysis?
            <Button autoFocus color="inherit"
            style={{fontSize:"16px",fontWeight:"600"}}
            onClick={()=>setAnalysis(true)}
            >
              View Analysis
              
            </Button>
            :
            <Button autoFocus color="inherit"
            style={{fontSize:"16px",fontWeight:"600"}}
            onClick={()=>setAnalysis(false)}
            >
              View Solution
              
            </Button>:""
          }
            
          </Toolbar>
        </AppBar>
       <div className="container-fluid userModal p-0" style={{height:""}}>
         <div className="row m-0">
           <div className="col-md-10 p-0" style={{margin:"auto"}}>
             <div className="container-fluid p-0">
                <div className="row m-0">
                  <div className="col-md-12 pt-4" style={{textAlign: 'center',fontSize:"30px",fontWeight:"bold",color:"#cf404d"}}>
                      Quiz Name: {q.name}
                      <p style={{fontSize:"20px",color:"black",fontWeight:"500"}}>Faculty: {q.author_name}</p>
                    
                  </div>
                  <div className="col-6">
                    <p style={{fontSize:"18px",fontWeight:"500", padding:" 0px", fontFamily:"cursive",marginBottom:"5px"}}>Total Question: {q.no_of_questions}</p>
                    <p style={{fontSize:"18px",fontWeight:"500", padding:" 0px", fontFamily:"cursive"}}>Marks: {marks * 4} / {result.length * 4}</p>
                    </div>
                
                  <div className="col-6" style={{textAlign:"right"}}>
                  <p style={{fontSize:"18px",fontWeight:"500", padding:" 0px", fontFamily:"cursive",marginBottom:"5px"}}>Quiz Date: {q.startTime? q.startTime.split('T')[0]:""}</p>
                  <p style={{fontSize:"18px",fontWeight:"500", padding:" 0px", fontFamily:"cursive",marginRight:"51px"}}>Quiz Time: {q.startTime?q.startTime.split('T')[1]:""}</p>    
                  </div>
                  {analysis?
                  <div className="col-md-11 p-4 mt-4 mb-4" style={{marginTop:"20px", width:"100%", margin:"auto", color:"#fff",textAlign: 'center',alignItems: 'center'}}>
                  
                         <Chart
                         width={'500px'}
                         height={'300px'}
                         chartType="PieChart"
                         loader={<div>Loading Chart</div>}
                         style={{margin:"auto"}}
                         data={[
                           ['Result', 'Marks'],
                           ['Correct', marks],
                           ['Unanswered', unans],
                           ['Wrong Answer', result.length-marks-unans]
                    
                         ]}
                         options={{
                           title: 'Result Analysis',
                           // Just add this option
                           is3D: true,
                         }}
                         rootProps={{ 'data-testid': '2' }}
                       />

                       </div>
                        :
                  <div className="col-md-11 p-4 mt-4 mb-4" style={{marginTop:"20px", width:"100%", margin:"auto", backgroundColor:"#2d3e4f", color:"#fff"}}>
                         
                         
                        {present=="true"?<ResultStepper quiz={q} questions={result}/>:<p style={{margin:"auto", textAlign: 'center', fontSize:"20px", fontWeight:"700"}}>You have not attempted this quiz</p>}  
                         
                                 
                  </div>
}
                  </div>
                
                </div>
           </div>
         </div>
         
       </div>
       
      </Dialog>
    </div>
  );
}