import React from 'react';
import Layout from '../../components/Layout'
import { Col } from 'reactstrap';
import MyCard from '../../components/MyCard'
import styles from '../../css/About.css'

const About=()=>{
    return (
        <Layout>
        <Col md="9 " className="back2 ">
            <div className="aboutText">
                <h1 className="abouth1">About Quizee</h1>
                <br/>
                <h6 className="abouth6">Interesting Facts</h6>
            </div>
            <div className="container-fluid">
            <div className="row">
                <div className="col-md-4 aboutContent">
                <MyCard
                icon={'people'}
                cardtext={"There are two users i.e. Faculty who can upload the quiz and the student who can take the quiz."}
                cardtitle={'Users'}

                />
              
                </div>
                <div className="col-md-4 aboutContent">
                <MyCard
                icon={'chrome_reader_mode'}
                cardtext={"Quiz is basically based on mutiple choice questions which can be uploaded in multiple ways."}
                cardtitle={'Quiz'}

                />
                </div><div className="col-md-4 aboutContent">
                <MyCard
                icon={'equalizer'}
                cardtext={"Student and faculty can view the results of the quiz taken and plan accordingly."}
                cardtitle={'Result'}

                />
                </div>
                <div className="col-md-4 aboutContent">
                <MyCard
                icon={'timeline'}
                cardtext={"Analysis of the quiz will help the students to evaluate themsleves."}
                cardtitle={'Analysis'}

                />
                </div>
                </div>
                </div>
            </Col>
        
        </Layout>
            
    )
}
export default About;